;EdykjBF;AACF;AACA,iFAAiF;AACjF,iCAAiC;AACjC,+CAA+C;AAhvjB/C;EACE,mBAAA;EACA,qBAAA;EACA,WAAA;EACA,gBAAA;EACA,kBAAA;EACA,mBAAA;EACA,UAAA;AAkvjBF","file":"dialtone-globals.less","sourcesContent":["// Import dialtone style definitions as references globally so they can be used in\n// mixins. See: https://dialpad.design\n@import '@dialpad/dialtone/lib/build/less/dialtone.less';\n\n// Fonts path override\n#d-internal-config {\n  // @dialpad/dialtone/lib/dist/fonts/\n  @define-font-path: \"../../../dist/fonts/\";\n}\n\n// Reset base font size to 12px.\n:root {\n  --base--font-size: @fs12;\n}\n\n// Vue transition classes for code samples.\n.fade-enter-active, .fade-leave-active {\n  transition: opacity 0.18s ease-in-out;\n}\n.fade-enter, .fade-leave-to {\n  opacity: 0 !important;\n}\n.fade-enter-to, .fade-leave {\n  opacity: 1 !important;\n}\n\n.slide-down-enter-active {\n  transition: transform 0.28s ease-out;\n}\n\n.slide-down-leave-active {\n  transition: transform 0.28s ease-in;\n}\n\n.slide-down-enter {\n  transform: translateY(-100%);\n}\n\n.slide-down-leave-to {\n  transform: translateY(100%);\n}\n\n.pop-enter-active {\n  animation: pop .5s;\n}\n\n.pop-leave-active {\n  animation: pop .5s reverse;\n}\n\n@keyframes pop {\n  50%  { transform: scale(1.2); }\n}\n\n.shake-enter-active {\n  animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;\n  transform: translate3d(0, 0, 0);\n}\n\n@keyframes shake {\n  10%, 90% {\n    transform: translate3d(-4px, 0, 0);\n  }\n\n  20%, 80% {\n    transform: translate3d(8px, 0, 0);\n  }\n\n  30%, 50%, 70% {\n    transform: translate3d(-16px, 0, 0);\n  }\n\n  40%, 60% {\n    transform: translate3d(16px, 0, 0);\n  }\n}\n\n@keyframes fade-in {\n  0% {\n    opacity: 0\n  }\n  50% {\n    opacity: 0.5\n  }\n\n  100% {\n    opacity: 1\n  }\n}\n\n@keyframes fade-out {\n  0% {\n    opacity: 1\n  }\n  50% {\n    opacity: 0.5\n  }\n\n  100% {\n    opacity: 0\n  }\n}\n\n.fade-in {\n  animation: fade-in 0.18s ease-in-out;\n}\n\n.fade-out {\n  animation: fade-out 0.18s ease-in-out;\n}\n\n.d-zoom-enter, .d-zoom-leave-to {\n  .d-modal--animate();\n}\n\n.d-zoom__dialog-enter, .d-zoom__dialog-leave-to {\n  .d-modal__dialog--animate();\n}\n\n.d-zoom-enter-active, .d-zoom__dialog-enter-active {\n  .d-modal--animate-in();\n}\n\n.d-zoom-leave-active {\n  .d-modal--animate-out();\n}\n\n.d-zoom__dialog-leave-active {\n  .d-modal__dialog--animate-out();\n}\n\n/* Hiding class, making content visible only to screen readers but not visually */\n/* \"sr\" meaning \"screen-reader\" */\n/* https://css-tricks.com/inclusively-hidden/ */\n.sr-only:not(:focus):not(:active) {\n  clip: rect(0 0 0 0);\n  clip-path: inset(50%);\n  height: 1px;\n  overflow: hidden;\n  position: absolute;\n  white-space: nowrap;\n  width: 1px;\n}\n","/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */\n\n/* Document\n   ========================================================================== */\n\n/**\n * 1. Correct the line height in all browsers.\n * 2. Prevent adjustments of font size after orientation changes in iOS.\n */\n\nhtml {\n  line-height: 1.15; /* 1 */\n  -webkit-text-size-adjust: 100%; /* 2 */\n}\n\n/* Sections\n   ========================================================================== */\n\n/**\n * Remove the margin in all browsers.\n */\n\nbody {\n  margin: 0;\n}\n\n/**\n * Render the `main` element consistently in IE.\n */\n\nmain {\n  display: block;\n}\n\n/**\n * Correct the font size and margin on `h1` elements within `section` and\n * `article` contexts in Chrome, Firefox, and Safari.\n */\n\nh1 {\n  margin: 0.67em 0;\n  font-size: 2em;\n}\n\n/* Grouping content\n   ========================================================================== */\n\n/**\n * 1. Add the correct box sizing in Firefox.\n * 2. Show the overflow in Edge and IE.\n */\n\nhr {\n  box-sizing: content-box; /* 1 */\n  height: 0; /* 1 */\n  overflow: visible; /* 2 */\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n// stylelint-disable\npre {\n  font-family: monospace, monospace; /* 1 */\n  font-size: 1em; /* 2 */\n}\n\n/* Text-level semantics\n   ========================================================================== */\n\n/**\n * Remove the gray background on active links in IE 10.\n */\n\na {\n  background-color: transparent;\n}\n\n/**\n * 1. Remove the bottom border in Chrome 57-\n * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.\n */\n\nabbr[title] {\n  border-bottom: none; /* 1 */\n  text-decoration: underline; /* 2 */\n  text-decoration: underline dotted; /* 2 */\n}\n\n/**\n * Add the correct font weight in Chrome, Edge, and Safari.\n */\n\nb,\nstrong {\n  font-weight: bolder;\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\ncode,\nkbd,\nsamp {\n  font-family: monospace, monospace; /* 1 */\n  font-size: 1em; /* 2 */\n}\n//stylelint-enable\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n  font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n  position: relative;\n  font-size: 75%;\n  line-height: 0;\n  vertical-align: baseline;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\nsup {\n  top: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove the border on images inside links in IE 10.\n */\n\nimg {\n  border-style: none;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers.\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  margin: 0; /* 2 */\n  font-size: 100%; /* 1 */\n  font-family: inherit; /* 1 */\n  line-height: 1.15; /* 1 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n  overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n  text-transform: none;\n}\n\n/**\n * Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\n[type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button;\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  padding: 0;\n  border-style: none;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n  outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n  padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n  display: table; /* 1 */\n  box-sizing: border-box; /* 1 */\n  max-width: 100%; /* 1 */\n  padding: 0; /* 3 */\n  color: inherit; /* 2 */\n  white-space: normal; /* 1 */\n}\n\n/**\n * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n  vertical-align: baseline;\n}\n\n/**\n * Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  outline-offset: -2px; /* 2 */\n  -webkit-appearance: textfield; /* 1 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  font: inherit; /* 2 */\n  -webkit-appearance: button; /* 1 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n  display: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n  display: none;\n}\n","//\n//  DIALTONE\n//  TOKENS: SPACING\n//\n//  These are spacing tokens for Dialpad's design system Dialtone.\n//  For further documentation of these values, please visit:\n//  https://dialpad.design/tokens/spacing\n//\n//  ============================================================================\n//  $   SPACING UNITS\n//  ----------------------------------------------------------------------------\n@su0:                           0;          // 0\n@su1:                           0.1rem;     // 1px\n@su2:                           0.2rem;     // 2px\n@su4:                           0.4rem;     // 4px\n@su6:                           0.6rem;     // 6px\n@su8:                           0.8rem;     // 8px\n@su12:                          1.2rem;     // 12px\n@su16:                          1.6rem;     // 16px\n@su24:                          2.4rem;     // 24px\n@su32:                          3.2rem;     // 32px\n@su48:                          4.8rem;     // 48px\n@su64:                          6.4rem;     // 64px\n@su72:                          7.2rem;     // 72px\n@su84:                          8.4rem;     // 84px\n@su96:                          9.6rem;     // 96px\n@su102:                         10.2rem;    // 102px\n@su114:                         11.4rem;    // 114px\n@su128:                         12.8rem;    // 128px\n\n@spacing-units: {\n    su0:    @su0;\n    su1:    @su1;\n    su2:    @su2;\n    su4:    @su4;\n    su6:    @su6;\n    su8:    @su8;\n    su12:   @su12;\n    su16:   @su16;\n    su24:   @su24;\n    su32:   @su32;\n    su48:   @su48;\n    su64:   @su64;\n    su72:   @su72;\n    su84:   @su84;\n    su96:   @su96;\n    su102:  @su102;\n    su114:  @su114;\n    su128:  @su128;\n\n    sun1:   -(@su1);\n    sun2:   -(@su2);\n    sun4:   -(@su4);\n    sun6:   -(@su6);\n    sun8:   -(@su8);\n    sun12:  -(@su12);\n    sun16:  -(@su16);\n    sun24:  -(@su24);\n    sun32:  -(@su32);\n    sun48:  -(@su48);\n    sun64:  -(@su64);\n    sun72:  -(@su72);\n    sun84:  -(@su84);\n    sun96:  -(@su96);\n    sun102: -(@su102);\n    sun114: -(@su114);\n    sun128: -(@su128);\n}\n\nbody {\n    //  Spacing units\n    each(@spacing-units, {\n        --@{key}: @value;\n    });\n}\n","//\n//  DIALTONE\n//  COMPONENTS: Avatars\n//\n//  These are avatars classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/avatars\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//     SIZES\n//     COLORS\n//   ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-avatar {\n  //  Component CSS Vars\n  //  ------------------------------------------------------------------------\n  --avatar--fc: var(--fc-lighter);\n  --avatar--bgc: var(--fc-dark);\n  --avatar--size: var(--su12);\n\n  width: var(--avatar--size);\n  height: var(--avatar--size);\n  color: var(--avatar--fc);\n  background-color: var(--avatar--bgc);\n  border-radius: 50%;\n\n  img {\n    width: var(--avatar--size);\n    height: var(--avatar--size);\n    object-fit: cover;\n    border-radius: inherit;\n  }\n\n  //  --  SIZES\n  //  ------------------------------------------------------------------------\n  &.d-avatar--sm {\n    --avatar--size: var(--su24);\n  }\n\n  &.d-avatar--md {\n    --avatar--size: var(--su32);\n  }\n\n  &.d-avatar--lg {\n    --avatar--size: var(--su48);\n  }\n\n  //  --  COLORS\n  //  ------------------------------------------------------------------------\n  &.d-avatar--purple-800 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--purple-800);\n  }\n\n  &.d-avatar--purple-700 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--purple-700);\n  }\n\n  &.d-avatar--purple-600 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--purple-600);\n  }\n\n  &.d-avatar--purple-500 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--purple-500);\n  }\n\n  &.d-avatar--purple-400 {\n      --avatar--fc: var(--purple-800);\n      --avatar--bgc: var(--purple-400);\n  }\n\n  &.d-avatar--purple-300 {\n      --avatar--fc: var(--purple-800);\n      --avatar--bgc: var(--purple-300);\n  }\n\n  &.d-avatar--purple-200 {\n      --avatar--fc: var(--purple-800);\n      --avatar--bgc: var(--purple-200);\n  }\n\n  &.d-avatar--orange-500 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--orange-500);\n  }\n\n  &.d-avatar--orange-400 {\n      --avatar--fc: var(--orange-600);\n      --avatar--bgc: var(--orange-400);\n  }\n\n  &.d-avatar--orange-300 {\n      --avatar--fc: var(--orange-600);\n      --avatar--bgc: var(--orange-300);\n  }\n\n  &.d-avatar--orange-200 {\n      --avatar--fc: var(--orange-600);\n      --avatar--bgc: var(--orange-200);\n  }\n\n  &.d-avatar--pink-600 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--pink-600);\n  }\n\n  &.d-avatar--pink-500 {\n      --avatar--fc: var(--white);\n      --avatar--bgc: var(--pink-500);\n  }\n\n  &.d-avatar--pink-400 {\n      --avatar--fc: var(--pink-700);\n      --avatar--bgc: var(--pink-400);\n  }\n\n  &.d-avatar--pink-300 {\n      --avatar--fc: var(--pink-700);\n      --avatar--bgc: var(--pink-300);\n  }\n\n  &.d-avatar--yellow-500 {\n      --avatar--fc: var(--yellow-600);\n      --avatar--bgc: var(--yellow-500);\n  }\n\n  &.d-avatar--yellow-400 {\n      --avatar--fc: var(--yellow-600);\n      --avatar--bgc: var(--yellow-400);\n  }\n\n  &.d-avatar--yellow-300 {\n      --avatar--fc: var(--yellow-600);\n      --avatar--bgc: var(--yellow-300);\n  }\n\n  &.d-avatar--yellow-200 {\n      --avatar--fc: var(--yellow-600);\n      --avatar--bgc: var(--yellow-200);\n  }\n}\n\n\n//  ============================================================================\n//  $   ALTERNATE STYLES\n//  ============================================================================\n//  $$  INITIALS\n//  ----------------------------------------------------------------------------\n.d-avatar--initials {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-weight: var(--fw-bold);\n  font-family: inherit;\n  text-align: center;\n\n  //  --  SIZES\n  //  ------------------------------------------------------------------------\n  &.d-avatar--sm {\n    font-size: var(--fs12);\n  }\n\n  &.d-avatar--md {\n    font-size: var(--fs16);\n  }\n\n  &.d-avatar--lg {\n    font-size: var(--fs24);\n  }\n}\n\n//  $$  ICON\n//  ----------------------------------------------------------------------------\n.d-avatar--icon {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  //  --  SIZES\n  //  ------------------------------------------------------------------------\n  &.d-avatar--sm {\n    .d-svg {\n      width: var(--su16);\n      height: var(--su16);\n    }\n  }\n\n  &.d-avatar--md {\n    .d-svg {\n      width: var(--su24);\n      height: var(--su24);\n    }\n  }\n\n  &.d-avatar--lg {\n    .d-svg {\n      width: var(--su32);\n      height: var(--su32);\n    }\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: BADGES\n//\n//  These are badge classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/badges\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//     COLORS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-badge {\n  //  Component CSS Vars\n  --badge--fc: var(--fc-dark);\n  --badge--bgc: var(--black-050);\n\n  display: inline-block;\n  min-width: calc(var(--su6) + var(--su1));\n  padding: calc(var(--su6) / 4) var(--su6);\n  color: var(--badge--fc);\n  font-weight: bold;\n  font-size: var(--fs10);\n  line-height: var(--lh6);\n  text-align: center;\n  text-transform: uppercase;\n  background-color: var(--badge--bgc);\n  border-radius: var(--su4);\n}\n\n\n//  ============================================================================\n//  $   STYLE VARIATIONS\n//  ----------------------------------------------------------------------------\n.d-badge--white {\n  --badge--bgc: var(--white);\n}\n\n.d-badge--black-700 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--black-700);\n}\n\n.d-badge--purple-100 {\n  --badge--bgc: var(--purple-100);\n}\n\n.d-badge--purple-300 {\n  --badge--bgc: var(--purple-300);\n}\n\n.d-badge--purple-500 {\n  --badge--bgc: var(--purple-500);\n  --badge--fc: var(--white);\n}\n\n.d-badge--purple-700 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--purple-700);\n}\n\n.d-badge--orange-400 {\n  --badge--bgc: var(--orange-400);\n}\n\n.d-badge--pink-300 {\n  --badge--bgc: var(--pink-300);\n}\n\n.d-badge--pink-500 {\n  --badge--bgc: var(--pink-500);\n}\n\n.d-badge--pink-600 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--pink-600);\n}\n\n.d-badge--pink-700 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--pink-700);\n}\n\n.d-badge--yellow-300 {\n  --badge--bgc: var(--yellow-300);\n}\n\n.d-badge--green-400 {\n  --badge--bgc: var(--green-400);\n}\n\n.d-badge--red-500 {\n  --badge--fc: var(--white);\n  --badge--bgc: var(--red-500);\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BANNERS\n//\n//  These are the styles for banners, which are a type of notice used\n//  to alert users about important information relevant to them.\n//  For further documentation of these styles, please visit:\n//  https://dialpad.design/components/banners\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n.d-banner {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --banner--bgc: var(--black-025);\n    --banner--fc: var(--fc-dark);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    left: 0;\n    z-index: var(--zi-navigation-fixed);\n    display: flex;\n    box-sizing: border-box;\n    width: 100%;\n    min-height: var(--su48);\n    color: var(--banner--fc);\n    font-size: var(--fs14);\n    line-height: var(--lh6);\n    background-color: var(--banner--bgc);\n    border-radius: 0;\n    box-shadow: none;\n\n    // If you want to hide and reveal the banner\n    &[aria-hidden='true'] {\n        --topbar-height: var(--sun64);\n\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n\n    //  If you want to put the banner above the topbar\n    &.d-banner--pinned {\n        z-index: calc(~'var(--zi-navigation-fixed) + 1');\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n.d-banner--transform {\n    transform: translate3d(0, var(--topbar-height), 0);\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-banner__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n    width: 100%;\n    max-width: 128rem;\n    min-height: 100%;\n    margin: 0 auto;\n    padding: var(--su8);\n\n    .d-notice__content {\n        flex-direction: row;\n        align-items: center;\n\n        .d-flow4();\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-banner.d-banner--important {\n    --banner--bgc: var(--black-800);\n    --banner--fc: var(--black-025);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-banner--error {\n    --banner--bgc: var(--red-100);\n    --banner--fc: var(--red-700);\n\n    &.d-banner--important {\n        --banner--bgc: var(--red-500);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-banner--info {\n    --banner--bgc: var(--purple-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--purple-500);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-banner--success {\n    --banner--bgc: var(--green-100);\n    --banner--fc: var(--green-700);\n\n    &.d-banner--important {\n        --banner--bgc: var(--green-300);\n        --banner--fc: var(--green-700);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-banner--warning {\n    --banner--bgc: var(--yellow-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--yellow-400);\n        --banner--fc: var(--fc-dark);\n    }\n}\n","//@import (reference) '../variables/spacing.less';\n//@import (reference) 'internals.less';\n\n//\n//  DIALTONE\n//  UTILITIES: SPACING\n//\n//  These are spacing utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/spacing\n//\n//  TABLE OF CONTENTS\n//   AUTO-SPACING\n//    - VERTICALLY\n//    - HORIZONTALLY\n//   MARGIN\n//    - POSITIVE MARGINS\n//       ALL DIRECTIONS\n//       TOP MARGIN\n//       RIGHT MARGIN\n//       BOTTOM MARGIN\n//       LEFT MARGIN\n//       X-AXIS MARGIN\n//       Y-AXIS MARGIN\n//    - NEGATIVE MARGINS\n//       ALL DIRECTIONS\n//       TOP MARGIN\n//       RIGHT MARGIN\n//       BOTTOM MARGIN\n//       LEFT MARGIN\n//       X-AXIS MARGIN\n//       Y-AXIS MARGIN\n//   PADDING\n//    - ALL DIRECTIONS\n//    - TOP PADDING\n//    - RIGHT PADDING\n//    - BOTTOM PADDING\n//    - LEFT PADDING\n//    - X-AXIS PADDING\n//    - Y-AXIS PADDING\n//    - RESET PADDING\n//\n//  ============================================================================\n//  $   AUTO-SPACING\n//  ============================================================================\n//  $$  THE STACK\n//      Instead of stlying specific elements when we're unaware of their context,\n//      this applies margin to context. The margin is only applied when an\n//      element is proceeded by another element.\n//  ----------------------------------------------------------------------------\n.d-stack0 { > * + * { margin-top: @su0 !important; } }\n.d-stack1 { > * + * { margin-top: @su1 !important; } }\n.d-stack2 { > * + * { margin-top: @su2 !important; } }\n.d-stack4 { > * + * { margin-top: @su4 !important; } }\n.d-stack6 { > * + * { margin-top: @su6 !important; } }\n.d-stack8 { > * + * { margin-top: @su8 !important; } }\n.d-stack12 { > * + * { margin-top: @su12 !important; } }\n.d-stack16 { > * + * { margin-top: @su16 !important; } }\n.d-stack24 { > * + * { margin-top: @su24 !important; } }\n.d-stack32 { > * + * { margin-top: @su32 !important; } }\n.d-stack48 { > * + * { margin-top: @su48 !important; } }\n.d-stack64 { > * + * { margin-top: @su64 !important; } }\n\n.d-flow0 { > * + * { margin-left: @su0 !important; } }\n.d-flow1 { > * + * { margin-left: @su1 !important; } }\n.d-flow2 { > * + * { margin-left: @su2 !important; } }\n.d-flow4 { > * + * { margin-left: @su4 !important; } }\n.d-flow6 { > * + * { margin-left: @su6 !important; } }\n.d-flow8 { > * + * { margin-left: @su8 !important; } }\n.d-flow12 { > * + * { margin-left: @su12 !important; } }\n.d-flow16 { > * + * { margin-left: @su16 !important; } }\n.d-flow24 { > * + * { margin-left: @su24 !important; } }\n.d-flow32 { > * + * { margin-left: @su32 !important; } }\n.d-flow48 { > * + * { margin-left: @su48 !important; } }\n.d-flow64 { > * + * { margin-left: @su64 !important; } }\n\n\n//  ============================================================================\n//     MARGIN\n//  ============================================================================\n//  ============================================================================\n//  $  POSITIVE MARGINS\n//  ============================================================================\n//  $$ ALL DIRECTIONS\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-m',\n    { .template(@value) { margin: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n//  $$ TOP MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mt',\n    { .template(@value) { margin-top: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n//  $$ RIGHT MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mr',\n    { .template(@value) { margin-right: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n//  $$ BOTTOM MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mb',\n    { .template(@value) { margin-bottom: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n//  $$ LEFT MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-ml',\n    { .template(@value) { margin-left: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n//  $$ X-AXIS (LEFT & RIGHT)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mx',\n    { .template(@value) { margin-right: @value !important; margin-left: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n//  $$ Y-AXIS (TOP & BOTTOM)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-my',\n    { .template(@value) { margin-top: @value !important; margin-bottom: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 auto unset\n);\n\n\n//  ============================================================================\n//  $  NEGATIVE MARGINS\n//  ============================================================================\n//  $$ ALL NEGATIVE DIRECTIONS\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-m',\n    { .template(@value) { margin: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n\n//  $$ TOP NEGATIVE MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mt',\n    { .template(@value) { margin-top: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n//  $$ RIGHT NEGATIVE MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mr',\n    { .template(@value) { margin-right: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n//  $$ BOTTOM NEGATIVE MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mb',\n    { .template(@value) { margin-bottom: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n//  $$ LEFT NEGATIVE MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-ml',\n    { .template(@value) { margin-left: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n//  $$ X-AXIS NEGATIVE MARGIN (LEFT & RIGHT)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mx',\n    { .template(@value) { margin-right: @value !important; margin-left: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n//  $$ Y-AXIS NEGATIVE MARGIN (TOP & BOTTOM)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-my',\n    { .template(@value) { margin-top: @value !important; margin-bottom: @value !important; } },\n    -@su1 -@su2 -@su4 -@su6 -@su8 -@su12 -@su16 -@su24 -@su32 -@su48 -@su64\n);\n\n\n\n//  ============================================================================\n//     PADDING\n//  ============================================================================\n//  ============================================================================\n//  $  ALL DIRECTIONS\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-p',\n    { .template(@value) { padding: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n\n//  ============================================================================\n//  $  TOP PADDING\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-pt',\n    { .template(@value) { padding-top: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n\n//  ============================================================================\n//  $  RIGHT PADDING\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-pr',\n    { .template(@value) { padding-right: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n\n//  ============================================================================\n//  $  BOTTOM PADDING\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-pb',\n    { .template(@value) { padding-bottom: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n\n//  ============================================================================\n//  $  LEFT PADDING\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-pl',\n    { .template(@value) { padding-left: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n\n//  ============================================================================\n//  $  X-AXIS (LEFT & RIGHT)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-px',\n    { .template(@value) { padding-right: @value !important; padding-left: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n\n//  ============================================================================\n//  $  Y-AXIS (TOP & BOTTOM)\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-py',\n    { .template(@value) { padding-top: @value !important; padding-bottom: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 unset\n);\n","//\n//  DIALTONE\n//  COMPONENTS: BREADCRUMBS\n//\n//  These are breadcrumb classes for Dialpad's design system Dialtone.\n//  For further information, visit https://dialpad.design/components/breadcrumbs\n//\n//  TABLE OF CONTENTS\n//   WRAPPER\n//   ITEMS & LINKS\n//   INVERTED STYLE\n//\n//  ============================================================================\n//  $  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs > ol {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: flex-start;\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    font-size: var(--fs14);\n    line-height: var(--lh6);\n    list-style: none;\n}\n\n//  ============================================================================\n//  $  ITEMS & LINKS\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs {\n    //  --  INDIVIDUAL ITEMS\n    .d-breadcrumbs__item {\n        position: relative;\n        margin: 0;\n        margin-left: var(--su6);\n        padding: 0;\n        list-style: none;\n\n        //  Provide a forward slash with each element except when it's the last one.\n        &:not(:last-of-type) {\n            margin-right: var(--su12);\n\n            &::before {\n                position: absolute;\n                right: var(--sun12);\n                margin-top: var(--su1);\n                color: var(--muted-color);\n                content: '/';\n            }\n        }\n    }\n\n    //  --  SELECTED STATE (CURRENT PAGE)\n    .d-breadcrumbs__item--selected .d-link {\n        text-decoration: none;\n        cursor: default;\n    }\n}\n\n\n//  ============================================================================\n//  $  INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs.d-breadcrumbs--inverted {\n    //  --  ITEM\n    .d-breadcrumbs__item:not(:last-of-type)::before {\n        color: var(--inverted-color);\n    }\n\n    //  --  SELECTED STATE (CURRENT PAGE)\n    .d-breadcrumbs__item--selected .d-link {\n        &:hover {\n            color: var(--inverted-color);\n        }\n    }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BUTTONS\n//\n//  These are button classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/buttons\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//   VISUAL STYLES\n//   ALTERNATE STYLES\n//   SIZES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-btn {\n    //  Component specific CSS Vars\n    //  ------------------------------------------------------------------------\n    --button--fc: var(--primary-color);\n    --button--bgc: transparent;\n    --button--bc: transparent;\n    --button--br: var(--base--corner-radius);\n\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    padding: calc(var(--su8) - var(--su1));  // Subtract border\n    color: var(--button--fc);\n    font-size: var(--fs16);\n    font-family: inherit;\n    line-height: var(--lh4);\n    text-transform: inherit;\n    text-decoration: none;\n    vertical-align: middle;\n    background-color: var(--button--bgc);\n    border: 0.1rem solid var(--button--bc);\n    border-radius: var(--button--br);\n    box-shadow: var(--button--bs);\n    cursor: pointer;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: 75ms;\n    transition-property: background-color, border, box-shadow;\n    user-select: none;\n    fill: currentColor;\n\n    //  --  STATES\n    //  ------------------------------------------------------------------------\n    &:hover {\n        --button--fc: var(--primary-color-hover);\n        --button--bgc: hsla(var(--primary-color-hsl) ~' / ' 3%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--primary-color-hover);\n        --button--bgc: hsla(var(--primary-color-hsl) ~' / ' 9%);\n    }\n\n\n    &:focus-visible {\n      outline: none;\n      box-shadow: var(--bs-focus-ring);\n    }\n\n    &[disabled] {\n        color: var(--black-400);\n        background-color: var(--black-100);\n        border-color: transparent;\n        cursor: not-allowed;\n        transition: none;\n    }\n\n    .d-btn__icon .d-svg {\n        width: @icon16;\n        height: @icon16;\n    }\n}\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n//  $$  EXTRA SMALL\n//  ----------------------------------------------------------------------------\n.d-btn--xs {\n    padding: calc(var(--su6) - var(--su1)) calc(var(--su8) - var(--su1));  // Subtract border\n    font-size: var(--fs12);\n\n    .d-btn__icon .d-svg {\n        width: @icon12;\n        height: @icon12;\n    }\n}\n\n//  $$ SMALL\n//  ----------------------------------------------------------------------------\n.d-btn--sm {\n    padding: calc(var(--su6) - var(--su1)) calc(var(--su8) - var(--su1));  // Subtract border\n    font-size: var(--fs14);\n    line-height: var(--lh6);\n\n    .d-btn__icon .d-svg {\n        width: @icon14;\n        height: @icon14;\n    }\n}\n\n//  $$  LARGE\n//  ----------------------------------------------------------------------------\n.d-btn--lg {\n    padding: calc(var(--su12) - var(--su1)) calc(var(--su16) - var(--su1)); // Subtract border\n    font-size: var(--fs20);\n\n    .d-btn__icon .d-svg {\n        width: @icon20;\n        height: @icon20;\n    }\n}\n\n//  $$  EXTRA LARGE\n//  ----------------------------------------------------------------------------\n.d-btn--xl {\n    padding: calc(var(--su12) - var(--su1)) calc(var(--su16) - var(--su1)); // Subtract border\n    font-size: var(--fs24);\n    line-height: var(--lh8);\n\n    .d-btn__icon .d-svg {\n        width: @icon24;\n        height: @icon24;\n    }\n}\n\n//  ============================================================================\n//  $   BUTTON LABEL\n//  ----------------------------------------------------------------------------\n.d-btn__label {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n}\n\n//  ============================================================================\n//  $   BUTTON WITH ICON\n//  ----------------------------------------------------------------------------\n.d-btn--vertical {\n    flex-direction: column;\n}\n\n.d-btn__icon {\n    display: flex;\n    line-height: 0;\n}\n\n.d-btn__icon--left,\n.d-btn__icon--right {\n    margin-top: var(--sun4);\n    margin-bottom: var(--sun4);\n}\n\n.d-btn__icon--top,\n.d-btn__icon--bottom {\n    margin-right: var(--sun4);\n    margin-left: var(--sun4);\n}\n\n.d-btn__icon--left,\n.d-btn__icon--top {\n    order: -1;\n}\n\n.d-btn__icon--right,\n.d-btn__icon--bottom {\n    order: 1;\n}\n\n.d-btn__icon--left:not(:only-child) {\n    margin-left: var(--sun2);\n}\n\n.d-btn__icon--right:not(:only-child) {\n    margin-right: var(--sun2);\n}\n\n.d-btn__icon--left ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--right {\n    margin-left: var(--su4);\n}\n\n.d-btn__icon--right ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--left {\n    margin-right: var(--su4);\n}\n\n.d-btn__icon--top ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--bottom {\n    margin-top: var(--su4);\n}\n\n.d-btn__icon--bottom ~ .d-btn__label,\n.d-btn__label ~ .d-btn__icon--top {\n    margin-bottom: var(--su4);\n}\n\n//  ============================================================================\n//  $   VISUAL STYLES\n//  ----------------------------------------------------------------------------\n//  $$  CIRCLE BUTTONS\n//  ----------------------------------------------------------------------------\n.d-btn--circle {\n    --button--fc: var(--black-800);\n    --button--br: var(--br-circle);\n\n    padding: calc(var(--su8) + var(--su1));\n    transition-duration: 150ms;\n\n    &:hover {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 3%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 9%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-circle);\n    }\n\n    .d-btn__icon {\n        margin: unset;\n    }\n\n    &.d-btn__label {\n        display: none;\n    }\n\n    &.d-btn--outlined {\n        --button--bc: var(--black-700);\n    }\n\n    //  Adjust padding based on sizes\n    &.d-btn--xs {\n        padding: calc(var(--su8) - var(--su1));\n\n        --button--br: var(--br-circle);\n    }\n\n    &.d-btn--sm {\n        padding: var(--su8);\n\n        --button--br: var(--br-circle);\n    }\n\n    &.d-btn--lg {\n        padding: calc(var(--su12) + var(--su1));\n\n        --button--br: var(--br-circle);\n    }\n\n    &.d-btn--xl {\n        padding: calc(var(--su16) - var(--su1));\n\n        --button--br: var(--br-circle);\n    }\n}\n\n//  $$  OUTLINED BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--outlined {\n    --button--bc: var(--primary-color);\n}\n\n//  $$  PRIMARY BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--primary {\n    --button--fc: var(--white);\n    --button--bgc: var(--primary-color);\n\n    &:hover {\n      --button--fc: var(--white);\n      --button--bgc: hsl(var(--primary-color-h), var(--primary-color-s), 65%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n      --button--fc: var(--white);\n      --button--bgc: hsl(var(--primary-color-h), var(--primary-color-s), 42%);\n    }\n}\n\n//  $$  MUTED BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--muted {\n    --button--fc: var(--muted-color);\n\n    &:hover {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 3%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--muted-color-hover);\n        --button--bgc: hsla(var(--black-800-hsl) ~' / ' 9%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-muted);\n    }\n\n    &.d-btn--outlined {\n        --button--bc: var(--black-500);\n    }\n}\n\n//  $$  DANGER BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--danger {\n    --button--fc: var(--error-color);\n\n    &:hover {\n        --button--fc: var(--error-color-hover);\n        --button--bgc: hsla(var(--error-color-hsl) ~' / ' 6%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--error-color-hover);\n        --button--bgc: hsla(var(--error-color-hsl) ~' / ' 20%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-error);\n    }\n\n    &.d-btn--outlined {\n        --button--bc: var(--error-color);\n    }\n\n    &.d-btn--primary {\n        --button--fc: var(--white);\n        --button--bgc: var(--error-color);\n\n        &:hover {\n            --button--fc: var(--white);\n            --button--bgc: hsl(var(--red-500-h), var(--red-500-s), 40%);\n        }\n\n        &:active,\n        &.d-btn--active,\n        &.d-btn--active:active {\n            --button--fc: var(--white);\n            --button--bgc: hsl(var(--red-500-h), var(--red-500-s), 27%);\n        }\n    }\n}\n\n//  $$  INVERTED BUTTON\n//  ----------------------------------------------------------------------------\n//  --  Base, clear inverted button\n.d-btn--inverted {\n    --button--fc: var(--white);\n    --button--bgc: transparent;\n\n    &:hover {\n        --button--fc: var(--white);\n        --button--bgc: hsla(var(--white-hsl) ~' / ' 15%);\n    }\n\n    &:active,\n    &.d-btn--active,\n    &.d-btn--active:active {\n        --button--fc: var(--white);\n        --button--bgc: hsla(var(--white-hsl) ~' / ' 30%);\n    }\n\n    &:focus-visible {\n        box-shadow: var(--bs-focus-ring-inverted);\n    }\n\n    //  --  OUTLINED\n    &.d-btn--outlined {\n        --button--bc: var(--white);\n    }\n\n    //  --  PRIMARY\n    &.d-btn--primary {\n        --button--fc: var(--primary-color);\n        --button--bgc: hsl(var(--purple-100-h), var(--purple-100-s), 100%);\n\n        &:hover {\n            --button--fc: var(--primary-color-hover);\n            --button--bgc: hsl(var(--purple-100-h), var(--purple-100-s), 94%);\n        }\n\n        &:active,\n        &.d-btn--active,\n        &.d-btn--active:active {\n            --button--fc: var(--primary-color-hover);\n            --button--bgc: hsl(var(--purple-100-h), var(--purple-100-s), 91%);\n        }\n    }\n}\n\n//  $$  ICON ONLY\n//  ----------------------------------------------------------------------------\n.d-btn--icon-only {\n    padding: calc(var(--su8) + var(--su1));\n\n    .d-btn__icon {\n        margin: unset;\n    }\n}\n\n//  $$  LOADING STATE\n//  ----------------------------------------------------------------------------\n.d-btn--loading {\n    .d-btn-loading();\n}\n\n//  $$  DISABLED STATE\n//  ----------------------------------------------------------------------------\n.d-btn--disabled {\n    color: var(--black-400);\n    background-color: var(--black-100);\n    border-color: transparent;\n    cursor: not-allowed;\n    transition: none;\n    pointer-events: none;\n}\n\n//  $$  BRAND BUTTONS\n//  ----------------------------------------------------------------------------\n.d-btn--brand {\n    display: flex;\n    color: var(--white);\n    background-color: hsl(var(--brand-color-h) var(--brand-color-s) var(--brand-color-l));\n\n    &:hover,\n    &:active {\n        color: hsla(var(--white-hsl) ~' / ' 90%);\n        background-color: hsl(var(--brand-color-h) calc(var(--brand-color-s) + 2.5%) calc(var(--brand-color-l) - 5%));\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su1) var(--white), 0 0 0 0.25em hsla(var(--brand-color-h) var(--brand-color-s) var(--brand-color-l) ~' / ' 90%);\n    }\n\n    &:active {\n        background-color: hsl(var(--brand-color-h) calc(var(--brand-color-s) + 5%) calc(var(--brand-color-l) - 10%));\n    }\n\n    .d-btn__icon {\n      align-items: center;\n      justify-content: center;\n      width: 2em;\n      height: 2em;\n      margin-right: var(--su8);\n      margin-left: var(--sun4);\n      background-color: var(--white);\n      border-radius: var(--br2);\n    }\n\n    .d-btn__label {\n        flex: 1 auto;\n    }\n}\n\n//  --  GOOGLE\n.d-btn--google {\n    --brand-color-h: 217;\n    --brand-color-s: 89%;\n    --brand-color-l: 61%;\n}\n//  --  OFFICE 365\n.d-btn--o365 {\n    --brand-color-h: 15;\n    --brand-color-s: 99%;\n    --brand-color-l: 46%;\n}\n//  --  LINKEDIN\n.d-btn--linkedin {\n    --brand-color-h: 197;\n    --brand-color-s: 100%;\n    --brand-color-l: 26%;\n}\n\n//  ============================================================================\n//  @   BUTTON LOADER\n//      Changes the button\n//  ----------------------------------------------------------------------------\n//  @@  BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-btn-loading() {\n    //  Disable clicking while the loader is in place\n    pointer-events: none;\n\n    //  Hide anything that was there before\n    .d-btn__icon,\n    .d-btn__label {\n        opacity: 0;\n        transition: opacity 50ms var(--ttf-in-out);\n    }\n\n    //  Show the loading animation\n    &::before {\n        position: absolute;\n        width: var(--su16);\n        height: var(--su16);\n        border: var(--su2) solid currentColor;\n        border-left-color: transparent !important;\n        border-radius: var(--br-circle);\n        animation: d-loading-circle 900ms infinite linear;\n        content: '';\n    }\n}\n\n//  ============================================================================\n//  $   BUTTON GROUP\n//  ----------------------------------------------------------------------------\n.d-btn-group {\n    display: flex;\n\n    &.d-btn-group--start {\n        justify-content: start;\n    }\n\n    &.d-btn-group--end {\n        justify-content: end;\n    }\n\n    &.d-btn-group--space-between {\n        justify-content: space-between;\n    }\n}\n","//@import (reference) '../variables/typography.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: FORMS\n//\n//  These are form classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/forms\n//\n//  TABLE OF CONTENTS\n//   FIELDSETS\n//   LABELS\n//    - Sizes\n//   DESCRIPTIONS\n//    - Sizes\n//   VALIDATION MESSAGES\n//    - Validation States\n//\n//  ============================================================================\n//  $  FIELDSETS\n//  ----------------------------------------------------------------------------\nfieldset {\n  min-width: var(--su0);\n  padding: var(--su0);\n  border: none;\n}\n\n\n//  ============================================================================\n//  $  LABELS\n//  ----------------------------------------------------------------------------\n.d-label {\n  display: flex;\n  flex: 1 0%;\n  align-items: baseline;\n  justify-content: space-between;\n  box-sizing: border-box;\n  margin-bottom: var(--su6);\n  color: currentColor;\n  font-weight: @fw-bold;\n  font-size: var(--fs16);\n  font-family: inherit;\n  line-height: var(--lh4);\n  word-break: break-word;\n  overflow-wrap: break-word;\n  cursor: pointer;\n\n  legend & { cursor: default; }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-label--xs {\n  font-size: var(--fs12);\n  line-height: var(--lh4);\n}\n\n.d-label--sm {\n  font-size: var(--fs14);\n  line-height: var(--lh2);\n}\n\n.d-label--lg {\n  font-size: var(--fs18);\n  line-height: var(--lh2);\n}\n\n.d-label--xl {\n  font-size: var(--fs20);\n  line-height: var(--lh4);\n}\n\n\n//  ============================================================================\n//  $ DESCRIPTIONS\n//  ----------------------------------------------------------------------------\n.d-description {\n  display: flex;\n  box-sizing: border-box;\n  margin-bottom: var(--su6);\n  color: var(--black-500);\n  font-size: var(--fs12);\n  font-family: inherit;\n  line-height: var(--lh4);\n  fill: currentColor;\n}\n\n.d-label + .d-description {\n  margin-top: var(--sun6);\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-description--lg {\n  font-size: var(--fs14);\n  line-height: var(--lh6);\n}\n\n.d-description--xl {\n  font-size: var(--fs16);\n  line-height: var(--lh6);\n}\n\n//  ============================================================================\n//  $   VALIDATION MESSAGES\n//  ----------------------------------------------------------------------------\n.d-validation-message {\n  --validation--fc: var(--fc-light);\n\n  display: inline-flex;\n  align-items: flex-start;\n  margin-top: var(--su6);\n  color: var(--validation--fc);\n  font-weight: inherit;\n  font-size: var(--fs12);\n  font-family: inherit;\n  line-height: var(--lh4);\n\n  // Icon Slot\n  &::before {\n    width: var(--su16);\n    min-width: var(--su16);\n    height: var(--su16);\n    margin-right: var(--su4);\n    content: '';\n  }\n}\n\n//  $$   VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-validation-message--warning {\n  --validation--fc: var(--fc-warning);\n\n  &::before {\n    background-image: url(\"data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 21h22L12 2 1 21zm12-3h-2v-2h2v2zm0-4h-2v-4h2v4z' fill='hsl(43 100% 33%)'/%3E%3C/svg%3E\");\n    background-repeat: no-repeat;\n  }\n}\n\n.d-validation-message--error {\n  --validation--fc: var(--fc-error);\n\n  &::before {\n    background-image: url(\"data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z' fill='hsl(0 100% 29%)'/%3E%3C/svg%3E\");\n    background-repeat: no-repeat;\n  }\n}\n\n.d-validation-message--success {\n  --validation--fc: var(--fc-success);\n\n  &::before {\n    background-image: url(\"data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z' fill='hsl(115 94% 18%)'/%3E%3C/svg%3E\");\n    background-repeat: no-repeat;\n  }\n}\n","//@import (reference) '../variables/icons.less';\n//@import (reference) '../variables/sizes.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: INPUTS\n//\n//  These are input classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/inputs\n//\n//  TABLE OF CONTENTS\n//   VARIABLES\n//   INPUTS & TEXTAREAS\n//    - Base Style\n//    - Wrapper\n//    - Validation States\n//    - Sizes\n//   INPUT ICONS\n//\n//  ============================================================================\n//  Inputs CSS Vars\n//  ----------------------------------------------------------------------------\n@inputs-icon-spacing--xs: calc(var(--su4) + @icon12 + var(--su8));\n@inputs-icon-spacing--sm: calc(var(--su4) + @icon14 + var(--su8));\n@inputs-icon-spacing:     calc(var(--su4) + @icon16 + var(--su8));\n@inputs-icon-spacing--lg: calc(var(--su4) + @icon20 + var(--su8));\n@inputs-icon-spacing--xl: calc(var(--su4) + @icon24 + var(--su8));\n\n//  $  INPUTS\n//  ----------------------------------------------------------------------------\n.d-input,\n.d-textarea {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --input--focus-bc: var(--primary-color);\n    --input--bc: var(--black-500);\n    --input--bgc: var(--white);\n    --input--fc: var(--black-800);\n\n    position: relative;\n    display: inline-flex;\n    flex: 1 0%;\n    box-sizing: border-box;\n    width: 100%;\n    min-width: 0;\n    padding: 0.7rem var(--su8);\n    color: var(--input--fc);\n    font-weight: inherit;\n    font-size: var(--fs16);\n    font-family: inherit;\n    line-height: var(--lh4);\n    background-color: var(--input--bgc);\n    border: 1px solid var(--input--bc);\n    border-radius: var(--base--corner-radius);\n    outline: none;\n    box-shadow: none;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: 100ms;\n    transition-property: border, box-shadow, background-color;\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing;\n    }\n\n    //  --  Placeholder copy\n    &::placeholder {\n        color: var(--black-300);\n    }\n    //  --  Remove input EDGE additions\n    &::-ms-clear {\n        display: none;\n    }\n    //  --  FOCUS\n    &:focus {\n        --input--bc: var(--input--focus-bc);\n\n        outline: 0;\n        box-shadow: var(--bs-focus-ring) !important;\n    }\n    //  --  DISABLED / READ-ONLY\n    &[disabled],\n    &[read-only] {\n        --input--bc: var(--black-200) !important;\n        --input--bgc: var(--black-075);\n        --input--fc: var(--black-400);\n\n        &:focus {\n            box-shadow: none !important;\n        }\n\n        //  --  Placeholder copy\n        &::placeholder {\n            color: var(--black-400);\n        }\n    }\n    //  --  DISABLED\n    &[disabled] {\n        cursor: not-allowed;\n    }\n}\n\n//  $$  INPUT WRAPPER\n//  ----------------------------------------------------------------------------\n.d-input__wrapper {\n    position: relative;\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-input.d-input--xs {\n    .input-button-xs();\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--xs;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--xs;\n    }\n}\n\n.d-input.d-input--sm {\n    .input-button-sm();\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--sm;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--sm;\n    }\n}\n\n.d-input.d-input--lg {\n    .input-button-lg();\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--lg;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--lg;\n    }\n}\n\n.d-input.d-input--xl {\n    .input-button-xl();\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--xl;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--xl;\n    }\n}\n\n\n//  ============================================================================\n//  $  TEXTAREAS\n//  ----------------------------------------------------------------------------\n.d-textarea {\n    min-height: 8rem;\n    vertical-align: top;\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-textarea--xs {\n    .input-button-xs();\n\n    min-height: 4rem;\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--xs;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--xs;\n    }\n}\n\n.d-textarea--sm {\n    .input-button-sm();\n\n    min-height: 4.8rem;\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--sm;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--sm;\n    }\n}\n\n.d-textarea--lg {\n    .input-button-lg();\n\n    min-height: 9.2rem;\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--lg;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--lg;\n    }\n}\n\n.d-textarea--xl {\n    .input-button-xl();\n\n    min-height: 10rem;\n\n    &.d-input-icon--right {\n        padding-right: @inputs-icon-spacing--xl;\n    }\n\n    &.d-input-icon--left {\n        padding-left: @inputs-icon-spacing--xl;\n    }\n}\n\n//  $$  VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-input--warning,\n.d-textarea--warning {\n    --input--bc: var(--warning-color) !important;\n    --input--focus-bc: var(--warning-color);\n\n    &:focus {\n        box-shadow: var(--bs-focus-ring-warning) !important;\n    }\n}\n\n.d-input--error,\n.d-textarea--error {\n    --input--bc: var(--error-color) !important;\n    --input--focus-bc: var(--error-color);\n\n    &:focus {\n        box-shadow: var(--bs-focus-ring-error) !important;\n    }\n}\n\n.d-input--success,\n.d-textarea--success {\n    --input--bc: var(--success-color) !important;\n    --input--focus-bc: var(--success-color);\n\n    &:focus {\n        box-shadow: var(--bs-focus-ring-success) !important;\n    }\n}\n\n//  ============================================================================\n//  $  INPUT ICONS\n//  ----------------------------------------------------------------------------\n.d-input-icon {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --input-icon-size: @icon16;\n\n    position: absolute;\n    z-index: var(--zi-base1);\n    display: inline-flex;\n    align-items: center;\n    height: 3.6rem;\n    margin: 0;\n    line-height: 0;\n\n    svg {\n        width: var(--input-icon-size);\n        height: var(--input-icon-size);\n    }\n\n    &.d-input-icon--left {\n        left: var(--su8);\n        // Update padding for d-input if d-input-icon--left is present\n        & ~ .d-input,\n        & ~ .d-textarea {\n            padding-left: @inputs-icon-spacing;\n        }\n    }\n\n    &.d-input-icon--right {\n        right: var(--su8);\n\n        // Update padding for d-input if d-input-icon--right is present\n        & ~ .d-input {\n            padding-right: @inputs-icon-spacing;\n        }\n    }\n}\n\n\n\n//  $$ SIZES\n//  ----------------------------------------------------------------------------\n.d-input-icon.d-input--xs {\n    height: 2.8rem;\n}\n\n.d-input-icon--xs {\n    --input-icon-size: @icon12;\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input, d-textarea if d-input-icon--left is present\n    &.d-input-icon--left ~ .d-input,\n    &.d-input-icon--left ~ .d-textarea {\n        padding-left: @inputs-icon-spacing--xs;\n    }\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input if d-input-icon--right is present\n    &.d-input-icon--right ~ .d-input {\n        padding-right: @inputs-icon-spacing--xs;\n    }\n}\n\n.d-input-icon.d-input--sm {\n    height: 3.2rem;\n}\n\n.d-input-icon--sm {\n    --input-icon-size: @icon14;\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input, d-textarea if d-input-icon--left is present\n    &.d-input-icon--left ~ .d-input,\n    &.d-input-icon--left ~ .d-textarea {\n        padding-left: @inputs-icon-spacing--sm;\n    }\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input if d-input-icon--right is present\n    &.d-input-icon--right ~ .d-input {\n        padding-right: @inputs-icon-spacing--sm;\n    }\n}\n\n.d-input-icon.d-input--lg {\n    height: 4.8rem;\n}\n\n.d-input-icon--lg {\n    --input-icon-size: @icon20;\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input, d-textarea if d-input-icon--left is present\n    &.d-input-icon--left ~ .d-input,\n    &.d-input-icon--left ~ .d-textarea {\n        padding-left: @inputs-icon-spacing--lg;\n    }\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input if d-input-icon--right is present\n    &.d-input-icon--right ~ .d-input {\n        padding-right: @inputs-icon-spacing--lg;\n    }\n}\n\n.d-input-icon.d-input--xl {\n    height: 5.6rem;\n}\n\n.d-input-icon--xl {\n    --input-icon-size: @icon24;\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input, d-textarea if d-input-icon--left is present\n    &.d-input-icon--left ~ .d-input,\n    &.d-input-icon--left ~ .d-textarea {\n        padding-left: @inputs-icon-spacing--xl;\n    }\n\n    // For backwards compatibility purposes only.\n    // Update padding for d-input if d-input-icon--right is present\n    &.d-input-icon--right ~ .d-input {\n        padding-right: @inputs-icon-spacing--xl;\n    }\n}\n","//\n//  DIALTONE\n//  TOKENS: SIZES\n//\n//  These are shared sizes for buttons, inputs, select menus (native),\n//  and textareas.\n//\n//  ============================================================================\n//  $   CSS VARS\n//  ============================================================================\n@input-button-vars: {\n    input-button__padding:                  0.7rem var(--su8);\n    input-button__text-size:                var(--fs16);\n    input-button__line-height:              var(--lh4);\n    input-button__icon-size:                @icon-size18;\n}\n\n//  ============================================================================\n//  $   SHARED INPUT & BUTTON SIZES\n//  ============================================================================\n//  $$  EXTRA SMALL\n//      Target height: 24px\n//  ----------------------------------------------------------------------------\n.input-button-xs() {\n    padding-top: 0.5rem;\n    padding-bottom: 0.5rem;\n    font-size: var(--fs12);\n\n    .d-btn__icon {\n        width: @icon-size14;\n        height: @icon-size14;\n    }\n}\n\n//  $$  SMALL\n//      Target height: 32px\n//  ----------------------------------------------------------------------------\n.input-button-sm() {\n    padding-top: var(--su6);\n    padding-bottom: var(--su6);\n    font-size: var(--fs14);\n\n    .d-btn__icon {\n        width: @icon-size16;\n        height: @icon-size16;\n    }\n}\n\n//  $$  LARGE\n//      Target height: 48px\n//  ----------------------------------------------------------------------------\n.input-button-lg() {\n    padding-top: 1.1rem;\n    padding-bottom: 1.1rem;\n    font-size: var(--fs20);\n\n    .d-btn__icon {\n        width: @icon-size20;\n        height: @icon-size20;\n    }\n}\n\n//  $$  EXTRA LARGE\n//      Target height: 56px\n//  ----------------------------------------------------------------------------\n.input-button-xl() {\n    padding-top: 1.3rem;\n    padding-bottom: 1.3rem;\n    font-size: var(--fs24);\n\n    .d-btn__icon {\n        width: @icon-size24;\n        height: @icon-size24;\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: LINKS\n//\n//  These are link classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/links\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//   ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-link {\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    margin: var(--su0);\n    padding: var(--su0);\n    color: var(--primary-color);\n    font: inherit;\n    text-decoration: underline;\n    background-color: transparent;\n    border: 0;\n    transition: all var(--td25) var(--ttf-in-out);\n\n    //  Reset button appearance\n    -webkit-appearance: none;\n       -moz-appearance: none;\n            appearance: none;\n    fill: currentColor;\n\n    &:hover {\n        color: var(--primary-color-hover);\n        text-decoration: none;\n        cursor: pointer;\n    }\n\n    &:focus-visible {\n        border-radius: var(--br4);\n        outline: none;\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring);\n    }\n}\n\n\n//  ============================================================================\n//  $   ALTERNATE STYLES\n//  ============================================================================\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-link--warning {\n    color: var(--fc-warning);\n\n    &:hover {\n        color: var(--fc-warning-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-warning);\n    }\n}\n\n//  $$  DANGER\n//  ----------------------------------------------------------------------------\n.d-link--danger {\n    color: var(--fc-error);\n\n    &:hover {\n        color: var(--error-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-error);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-link--success {\n    color: var(--fc-success);\n\n    &:hover {\n        color: var(--success-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-success);\n    }\n}\n\n//  $$  MUTED\n//  ----------------------------------------------------------------------------\n.d-link--muted {\n    color: var(--muted-color);\n\n    &:hover {\n        color: var(--muted-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-muted);\n    }\n}\n\n//  $$  DISABLED\n//  ----------------------------------------------------------------------------\n// We don't expose these in the documentation because they're meant to be consumed\n// by buttons using .d-link. Links with a disabled attribute are not valid mark-up.\n.d-link[disabled],\n.d-link--disabled {\n    color: var(--black-400);\n\n    &:hover {\n        color: var(--black-400);\n        text-decoration: underline;\n        cursor: not-allowed;\n    }\n\n    &:focus {\n        box-shadow: none;\n    }\n}\n\n//  $$  INVERTED\n//  ----------------------------------------------------------------------------\n.d-link--inverted {\n    color: var(--inverted-color);\n\n    &:hover {\n        color: var(--inverted-color-hover);\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--su2) var(--focus-ring-inverted);\n    }\n}\n\n//  $$  INVERTED DISABLED\n//  ----------------------------------------------------------------------------\n// We don't expose these in the documentation because they're meant to be consumed\n// by buttons using .d-link. Links with a disabled attribute are not valid mark-up.\n.d-link--inverted-disabled {\n    color: var(--black-400);\n\n    &:hover {\n        color: var(--black-400);\n        text-decoration: underline;\n        cursor: not-allowed;\n    }\n\n    &:focus {\n        box-shadow: none;\n    }\n}\n","//\n//  DIALTONE 5 SHIM\n//  COMPONENTS: LIST GROUPS\n//\n//  These are the list group definitions, which are used by classes or can be\n//  used within Vue components. For further documentation of these values,\n//  please visit https://dialpad.design/components/list-groups\n//\n//  ============================================================================\n.d-list-group,\n.d-list-group__item {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n}\n\n.d-list-group--header,\n.d-list-group--link {\n    display: block;\n    padding: var(--su4) var(--su24);\n}\n\n.d-list-group--header {\n    font-weight: var(--fw-bold);\n    font-size: var(--fs11);\n    text-transform: uppercase;\n}\n\n.d-list-group--link {\n    color: var(--black-800);\n    font-size: var(--fs14);\n    text-decoration: none;\n\n    &:hover {\n        color: var(--black-900);\n        background-color: hsla(var(--primary-color-hsl) ~' / ' 85%);\n    }\n\n    &-selected {\n        color: var(--white);\n        background-color: var(--primary-color);\n\n        &:hover {\n            color: var(--white);\n            background-color: hsl(var(--primary-color-h) var(--primary-color-s) calc(var(--primary-color-l) + 10%));\n        }\n    }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: MODALS\n//\n//  These are modal classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/modals\n//\n//  TABLE OF CONTENTS\n//   BASE STYLES\n//    - Wrapper\n//    - Dialog\n//    - Make them appear\n//   MODAL BACKGROUND\n//   HEADER\n//   CONTENT\n//   FOOTER\n//   TRANSITIONS\n//\n//  ============================================================================\n//  $   BASE STYLES\n//  ============================================================================\n//  $$  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-modal {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --modal--bgc: hsla(var(--black-900-hsl) ~' / ' 85%);\n    --modal-dialog--bgc: var(--white);\n    --modal-dialog--fc: var(--black-700);\n    --modal-header--fc: var(--black-800);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: var(--zi-hide);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    padding: var(--su32);\n    background-color: var(--modal--bgc);\n    backface-visibility: hidden;\n    visibility: hidden;\n    opacity: 0;\n    will-change: visibility, z-index, opacity;\n}\n\n.d-modal--transparent {\n  //  Component CSS Vars\n  //  ------------------------------------------------------------------------\n  --modal--bgc: hsla(var(--black-900-hsl) ~' / ' 85%);\n  --modal-dialog--bgc: var(--white);\n  --modal-dialog--fc: var(--black-700);\n  --modal-header--fc: var(--black-800);\n\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: var(--zi-hide);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: var(--su32);\n  background-color: var(--d-bgc-transparent);\n  backface-visibility: hidden;\n  visibility: hidden;\n  opacity: 0;\n  will-change: visibility, z-index, opacity;\n}\n\n//  $$  MODAL DIALOG\n//  ----------------------------------------------------------------------------\n.d-modal__dialog {\n  position: relative;\n  z-index: var(--zi-hide);\n  width: 100%;\n  max-width: var(--size628);\n  max-height: 100%;\n  padding: var(--su24);\n  overflow-y: auto;\n  color: var(--modal-dialog--fc);\n  font-size: var(--fs16);\n  line-height: var(--lh8);\n  background-color: var(--modal-dialog--bgc);\n  border-radius: var(--br8);\n  box-shadow: var(--bs-card);\n  transform: translate3d(0, 30%, 0) scale3d(0.75, 0.75, 0.75);\n  backface-visibility: hidden;\n  visibility: hidden;\n  opacity: 0;\n  will-change: visibility, z-index, opacity, transform;\n\n  .d-stack16();\n}\n\n//  $$   MAKE THEM APPEAR\n//  ----------------------------------------------------------------------------\n.d-modal[aria-hidden='false'],\n.d-modal[aria-hidden='false'] .d-modal__dialog,\n.d-modal--transparent[aria-hidden='false'],\n.d-modal--transparent[aria-hidden='false'] .d-modal__dialog {\n  z-index: var(--zi-modal);\n  transform: translate3d(0, 0, 0) scale3d(1, 1, 1);\n  visibility: visible;\n  opacity: 1;\n}\n\n\n//  ============================================================================\n//  $   MODAL AREAS\n//  ----------------------------------------------------------------------------\n.d-modal__footer {\n  display: flex;\n  flex-direction: row-reverse;\n  align-items: center;\n  margin: var(--sun4);\n\n  > * {\n    margin: var(--su4);\n  }\n}\n\n\n//  ============================================================================\n//  $   TYPE STYLES\n//  ============================================================================\n//  $$  HEADER\n//  ----------------------------------------------------------------------------\n.d-modal__header {\n  margin: 0 !important;\n  color: var(--modal-header--fc);\n  font-weight: var(--fw-bold);\n  font-size: var(--fs24);\n  line-height: var(--lh4);\n}\n\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-modal__content {\n  max-width: 75ch;\n  margin: 0;\n}\n\n\n//  ============================================================================\n//  $   BUTTONS\n//  ----------------------------------------------------------------------------\n.d-modal__close {\n  position: absolute;\n  top: var(--su8);\n  right: var(--su8);\n  margin: 0 !important;\n}\n\n\n//  ============================================================================\n//  $   BANNERS\n//  ----------------------------------------------------------------------------\n.d-modal__banner {\n  position: relative;\n  width: 100%;\n  max-width: var(--size628);\n  padding: var(--su12) var(--su24);\n  font-size: var(--fs14);\n  line-height: var(--lh4);\n  background-color: var(--yellow-200);\n  border-radius: var(--br8) var(--br8) 0 0;\n\n  &:not(.d-d-none) + .d-modal__dialog {\n    border-top-left-radius: 0;\n    border-top-right-radius: 0;\n  }\n}\n\n\n//  ============================================================================\n//  $   MODIFYING STYLES\n//  ============================================================================\n//  $$  FULL WIDTH\n//  ----------------------------------------------------------------------------\n.d-modal--full {\n  padding: 0;\n  overflow: hidden scroll;\n  transform: unset !important;\n\n  .d-modal__dialog {\n    display: flex;\n    flex-direction: column;\n    max-width: unset;\n    height: 100%;\n    padding: var(--su32);\n    border-radius: 0;\n    transform: unset !important;\n  }\n\n\n  .d-modal__footer {\n    margin: var(--sun16) var(--sun12);\n    margin-top: auto !important;\n  }\n\n  .d-modal__banner {\n    max-width: unset;\n    padding: var(--su12) var(--su32);\n    border-radius: 0;\n  }\n}\n\n// $$   SCROLLABLE\n//  ----------------------------------------------------------------------------\n.d-modal__dialog--scrollable {\n  display: flex;\n  flex-direction: column;\n\n  .d-modal__content {\n    overflow-y: auto;\n  }\n}\n\n//  $$  DANGER\n//  ----------------------------------------------------------------------------\n.d-modal--danger {\n  --modal-header--fc: var(--fc-error);\n}\n\n\n//  ============================================================================\n//  $   TRANSITIONS\n//  ----------------------------------------------------------------------------\n.d-modal--animate {\n  z-index: var(--zi-hide) !important;\n  backface-visibility: hidden !important;\n  visibility: hidden !important;\n  opacity: 0 !important;\n}\n\n.d-modal__dialog--animate {\n  .d-modal--animate();\n\n  transform: translate3d(0, 30%, 0) scale3d(0.75, 0.75, 0.75) !important;\n}\n\n.d-modal--animate-in,\n .d-modal__dialog--animate-in {\n  transition:\n    opacity 100ms var(--ttf-in-out) 10ms,\n    z-index 0s 0s,\n    visibility 0s 0s,\n    transform 100ms var(--ttf-in-out) 10ms;\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n\n.d-modal--animate-out {\n  transition:\n    opacity 100ms var(--ttf-in-out) 10ms,\n    z-index 0s 200ms,\n    visibility 0s 200ms,\n    transform 100ms var(--ttf-in-out) 10ms;\n\n  &.d-modal--full {\n    transition:\n      opacity 200ms var(--ttf-in-out) 0s,\n      z-index 0s 200ms,\n      visibility 0s 200ms;\n  }\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n\n.d-modal__dialog--animate-out {\n  transition:\n    opacity 200ms var(--ttf-in-out) 0s,\n    z-index 0s 200ms,\n    visibility 0s 200ms,\n    transform 100ms var(--ttf-in-out) 0s;\n\n  &.d-modal--full {\n    transition:\n      opacity 200ms var(--ttf-in-out) 0s,\n      z-index 0s 200ms,\n      visibility 0s 200ms;\n  }\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n","//@import (reference) '../utilities/spacing.less';\n//@import (reference) '../utilities/effects.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOAST\n//\n//  These are the styles for toast notices, which are used by alert users to\n//  information they might need. For further documentation of these styles,\n//  please visit https://dialpad.design/components/toasts\n\n//  ============================================================================\n//  @   TOAST CONTAINER\n//  ----------------------------------------------------------------------------\n.d-toast-wrapper {\n    position: absolute;\n    top: var(--su32);\n    left: 50%;\n    transform: translateX(-50%);\n\n    .d-stack8();\n}\n\n//  ============================================================================\n//  @   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toast {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --toast--bgc: var(--black-025);\n    --toast--fc: var(--fc-dark);\n\n    z-index: var(--zi-notification);\n    box-sizing: border-box;\n    width: 100%;\n    max-width: var(--size628);\n    margin-bottom: var(--su8);\n    padding: var(--su12);\n    color: var(--toast--fc);\n    font-size: var(--fs14);\n    line-height: var(--lh6);\n    word-break: normal;\n    background-color: var(--toast--bgc);\n    border-radius: var(--br4);\n    .d-bs-md();\n\n    //  If there's a link in a toast, break the url so it wraps\n    > a {\n        word-break: break-all;\n    }\n\n    // If you want to hide and reveal the toast\n    &[aria-hidden='true'] {\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-toast__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n\n    .d-notice__content {\n        margin-right: var(--su16);\n    }\n\n    .d-notice__actions {\n        margin-left: var(--su32);\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-toast.d-toast--important {\n    --toast--bgc: var(--black-800);\n    --toast--fc: var(--black-025);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-toast--error {\n    --toast--bgc: var(--red-100);\n    --toast--fc: var(--red-700);\n\n    &.d-toast--important {\n        --toast--bgc: var(--red-500);\n        --toast--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-toast--info {\n    --toast--bgc: var(--purple-100);\n    --toast--fc: var(--fc-dark);\n\n    &.d-toast--important {\n        --toast--bgc: var(--purple-500);\n        --toast--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-toast--success {\n    --toast--bgc: var(--green-100);\n    --toast--fc: var(--green-700);\n\n    &.d-toast--important {\n        --toast--bgc: var(--green-300);\n        --toast--fc: var(--green-700);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-toast--warning {\n    --toast--bgc: var(--yellow-100);\n\n    &.d-toast--important {\n        --toast--bgc: var(--yellow-400);\n        --toast--fc: var(--fc-dark);\n    }\n}\n\n//  ============================================================================\n//  $   UC Toast Shims\n//  ============================================================================\n//  $$  CHAT\n//  ----------------------------------------------------------------------------\n.d-toast--chat {\n  .d-notice__icon,\n .d-toast__meta {\n    color: var(--black-600);\n  }\n\n  .d-toast__meta {\n    font-size: var(--fs12);\n  }\n\n  &.d-toast--important {\n    .d-notice__icon,\n .d-toast__meta {\n      color: var(--black-025);\n    }\n  }\n}\n\n//  $$  ORGANIZER\n//  ----------------------------------------------------------------------------\n.d-toast--organizer {\n  //\n}\n\n//  $$  VIEWING\n//  ----------------------------------------------------------------------------\n.d-toast--viewing {\n //\n}\n\n.d-toast--organizer,\n.d-toast--viewing {\n  border-top: var(--su4) solid var(--pink-500);\n\n  .d-notice__icon {\n    color: var(--pink-500);\n  }\n}\n\n.d-toast--chat,\n.d-toast--organizer,\n.d-toast--viewing {\n  --toast--bgc: var(--white);\n\n  .d-notice__icon {\n    align-self: flex-start;\n  }\n\n  .d-notice__message {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n  }\n}\n","//@import (reference) '../variables/spacing.less';\n\n//\n//  DIALTONE\n//  UTILITIES: EFFECTS\n//\n//  These are effects utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/effects\n//\n//  TABLE OF CONTENTS\n//   ANIMATIONS\n//      - GENERIC LOADING\n//      - DIALPAD BAR ANIMATION\n//   BOX SHADOW\n//   OPACITY\n//   TRANSITION\n//\n//\n//  ============================================================================\n//  $   ANIMATIONS\n//  ----------------------------------------------------------------------------\n//  $$  GENERIC LOADING\n//      This could be applied to an empty div or a psuedo element (ie ::before)\n//  ----------------------------------------------------------------------------\n@keyframes d-loading-circle {\n    from {\n        transform: rotate(0deg);\n    }\n\n    to {\n        transform: rotate(360deg);\n    }\n}\n\n//  $$   DIALPAD BAR ANIMATION\n//  ----------------------------------------------------------------------------\n@keyframes d-loading-bars__short {\n  0% {\n    min-height: @su4;\n    transform: translateY(-@su16);\n    opacity: 0;\n  }\n\n  5% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0.5;\n  }\n\n  10% {\n    min-height: 1rem;\n    transform: translateY(-@su2);\n    opacity: 1;\n  }\n\n  15% {\n    transform: translateY(@su1);\n    opacity: 1;\n  }\n\n  20%,\n 70% {\n    min-height: 1rem;\n    transform: translateY(0);\n    opacity: 1;\n  }\n\n  75% {\n    min-height: 0.75rem;\n    transform: translateY(1rem);\n    opacity: 1;\n  }\n\n  80% {\n    min-height: @su4;\n    transform: translateY(-@su4);\n    opacity: 0.5;\n  }\n\n  85%,\n 100% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0;\n  }\n}\n\n@keyframes d-loading-bars__tall {\n  0% {\n    min-height: @su4;\n    transform: translateY(-@su16);\n    opacity: 0;\n  }\n\n  5% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0.5;\n  }\n\n  10% {\n    min-height: 1rem;\n    transform: translateY(-@su2);\n    opacity: 1;\n  }\n\n  15% {\n    min-height: 2.2rem;\n    transform: translateY(@su1);\n    opacity: 1;\n  }\n\n  20%,\n 70% {\n    min-height: 2.2rem;\n    transform: translateY(0);\n    opacity: 1;\n  }\n\n  75% {\n    min-height: @su16;\n    transform: translateY(1rem);\n    opacity: 1;\n  }\n\n  80% {\n    min-height: 1rem;\n    transform: translateY(-@su4);\n    opacity: 1;\n  }\n\n  85%,\n 100% {\n    min-height: @su4;\n    transform: translateY(@su16);\n    opacity: 0;\n  }\n}\n\n//  ============================================================================\n//  $   BOX SHADOW\n//  ----------------------------------------------------------------------------\n.d-bs-sm { box-shadow: var(--bs-sm) !important; }\n#d-internals #generate-hover-focus(d-bs-sm, {.d-bs-sm();});\n.d-bs-md { box-shadow: var(--bs-md) !important; }\n#d-internals #generate-hover-focus(d-bs-md, {.d-bs-md ();});\n.d-bs-lg { box-shadow: var(--bs-lg) !important; }\n#d-internals #generate-hover-focus(d-bs-lg, {.d-bs-lg();});\n.d-bs-xl { box-shadow: var(--bs-xl) !important; }\n#d-internals #generate-hover-focus(d-bs-xl, {.d-bs-xl();});\n.d-bs-card { box-shadow: var(--bs-card) !important; }\n#d-internals #generate-hover-focus(d-bs-card, {.d-bs-card();});\n.d-bs-none { box-shadow: none !important; }\n#d-internals #generate-hover-focus(d-bs-none, {.d-bs-none();});\n.d-bs-unset { box-shadow: unset !important; }\n#d-internals #generate-hover-focus(d-bs-unset, {.d-bs-unset();});\n\n\n//  ============================================================================\n//  $   OPACITY\n//      For times that you want a fill background, but not at 100% opacity.\n//      These are only offered for $white and gray.\n//  ----------------------------------------------------------------------------\n.d-o0 { opacity: 0 !important; }\n.d-o5 { opacity: 0.05 !important; }\n.d-o10 { opacity: 0.1 !important; }\n.d-o20 { opacity: 0.2 !important; }\n.d-o25 { opacity: 0.25 !important; }\n.d-o30 { opacity: 0.3 !important; }\n.d-o40 { opacity: 0.4 !important; }\n.d-o50 { opacity: 0.5 !important; }\n.d-o60 { opacity: 0.6 !important; }\n.d-o70 { opacity: 0.7 !important; }\n.d-o75 { opacity: 0.75 !important; }\n.d-o80 { opacity: 0.8 !important; }\n.d-o90 { opacity: 0.9 !important; }\n.d-o100 { opacity: 1 !important; }\n.d-o-unset { opacity: unset !important; }\n\n\n//  ============================================================================\n//  $   TRANSITION\n//  ----------------------------------------------------------------------------\n.d-t {\n    transition-delay: 0s;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: var(--td50);\n    transition-property: all;\n}\n\n//  --  TRANSITION DURATION\n.d-td0 { transition-duration: var(--td0) !important; }\n.d-td50 { transition-duration: var(--td50) !important; }\n.d-td100 { transition-duration: var(--td100) !important; }\n.d-td150 { transition-duration: var(--td150) !important; }\n.d-td200 { transition-duration: var(--td200) !important; }\n\n//  --  TRANSITION TIMING\n.d-ttf-in-out { transition-timing-function: var(--ttf-in-out) !important; }\n.d-ttf-out { transition-timing-function: var(--ttf-out) !important; }\n\n//  --  TRANSITION PROPERTY\n.d-tp-all { transition-property: all !important; }\n.d-tp-o { transition-property: opacity !important; }\n.d-tp-bs { transition-property: box-shadow !important; }\n.d-tp-bgc { transition-property: background-color !important; }\n.d-tp-transform { transition-property: transform !important; }\n.d-tp-colors { transition-property: background-color, border-color, color, fill, stroke !important; }\n\n//  --  TRANSITION DELAY\n.d-t-delay25 { transition-delay: var(--td25) !important; }\n.d-t-delay50 { transition-delay: var(--td50) !important; }\n.d-t-delay100 { transition-delay: var(--td100) !important; }\n.d-t-delay150 { transition-delay: var(--td150) !important; }\n.d-t-delay200 { transition-delay: var(--td200) !important; }\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: NOTICES\n//\n//  These are the styles for notices, which are used by alert users to\n//  information they might need. For further documentation of these styles,\n//  please visit https://dialpad.design/components/notices\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n\n.d-notice {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --notice--bgc: var(--black-025);\n    --notice--fc: var(--fc-dark);\n\n    display: flex;\n    align-items: center;\n\n    //  Base Styles\n    //  ------------------------------------------------------------------------\n    box-sizing: border-box;\n    width: 100%;\n    max-width: var(--size628);\n    padding: var(--su12);\n    color: var(--notice--fc);\n    font-size: var(--fs14);\n    line-height: var(--lh6);\n    background-color: var(--notice--bgc);\n    border-radius: var(--br4);\n\n    .d-toast & {\n        max-width: var(--size464);\n        box-shadow: var(--bs-sm);\n        pointer-events: all;\n    }\n}\n\n//  ============================================================================\n//  $   NOTICE AREAS\n//  ============================================================================\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-notice__content {\n    display: flex;\n    flex: 1 auto;\n    flex-direction: column;\n    margin-right: var(--su16);\n}\n\n//  $$  ACTIONS\n//  ----------------------------------------------------------------------------\n.d-notice__actions {\n    display: flex;\n    flex: 0 auto;\n    align-items: center;\n\n    .d-flow8();\n}\n\n//  $$  ICON\n//  ----------------------------------------------------------------------------\n.d-notice__icon {\n    display: flex;\n    flex: 0 auto;\n    margin-right: var(--su8);\n    color: inherit;\n}\n\n//  ============================================================================\n//  $   COPY STYLES\n//  ============================================================================\n.d-notice__title {\n    font-weight: var(--fw-bold);\n    font-size: inherit;\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-notice.d-notice--important {\n    --notice--bgc: var(--black-800);\n    --notice--fc: var(--black-025);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-notice--error,\n.d-banner--error,\n.d-toast--error {\n    --notice--bgc: var(--red-100);\n    --notice--fc: var(--red-700);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--red-500);\n        --notice--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-notice--info,\n.d-banner--info,\n.d-toast--info {\n    --notice--bgc: var(--purple-100);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--purple-500);\n        --notice--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-notice--success,\n.d-banner--success,\n.d-toast--success {\n    --notice--bgc: var(--green-100);\n    --notice--fc: var(--green-700);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--green-300);\n        --notice--fc: var(--green-700);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-notice--warning,\n.d-banner--warning,\n.d-toast--warning {\n    --notice--bgc: var(--yellow-100);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice--bgc: var(--yellow-400);\n        --notice--fc: var(--fc-dark);\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: POPOVER\n//\n//  These are popover classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/popover\n//\n//  TABLE OF CONTENTS\n//   POPOVER\n//   POPOVER DIALOG\n//    - Base dialog style\n//    - Content\n//    - Header / Footer\n\n\n//  $  POPOVER\n//  ----------------------------------------------------------------------------\n\n//  $  POPOVER DIALOG\n//  ----------------------------------------------------------------------------\n.d-popover__dialog {\n  &,\n  *,\n  *::before,\n  *::after {\n    box-sizing: border-box;\n  }\n\n  display: grid;\n  grid-template-rows: auto 1fr;\n  overflow: auto;\n  background-color: var(--white);\n  border-color: var(--black-100);\n  border-radius: var(--su8);\n  box-shadow: var(--bs-card);\n}\n\n// to be set on the dialog when it is modal\n.d-popover__dialog--modal {\n  z-index: var(--zi-modal-element);\n}\n\n//  $$  DIALOG CONTENT\n//  ----------------------------------------------------------------------------\n.d-popover__content {\n  overflow: auto;\n}\n\n//  $$  DIALOG HEADER / FOOTER\n//  ----------------------------------------------------------------------------\n.d-popover__header-footer-base {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  width: 100%;\n  min-height: var(--size48);\n  padding-top: var(--su6);\n  padding-bottom: var(--su6);\n  overflow: auto;\n  font-weight: var(--fw-bold);\n  font-size: var(--fs16);\n}\n\n.d-popover__header {\n  .d-popover__header-footer-base();\n\n  border-bottom: var(--su1) solid var(--black-075);\n}\n\n.d-popover__footer {\n  .d-popover__header-footer-base();\n\n  border-top: var(--su1) solid var(--black-075);\n}\n","//\n//  DIALTONE\n//  COMPONENTS: RADIOS & CHECKBOXES\n//\n//  These are input classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/inputs\n//\n//  TABLE OF CONTENTS\n//   SHARED STYLES\n//   WRAPPERS\n//   INPUT & COPY BLOCKS\n//   LABELS\n//   CHECKBOXES\n//    - Modifications\n//    - Validation States\n//   RADIOS\n//    - Modifications\n//    - Validation States\n//\n//  ============================================================================\n//  $   RADIOS & CHECKBOXES\n//  ----------------------------------------------------------------------------\n.d-checkbox,\n.d-radio {\n    //  Component specific CSS Vars\n    //  ------------------------------------------------------------------------\n    --check-radio--color: var(--primary-color);\n    --check-radio--bc: var(--black-500);\n    --check-radio--bgc: var(--white);\n\n    //  [1] Check to see if we can use custom styles, if so reset the defaults\n    //  ------------------------------------------------------------------------\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        width: 1em;\n        height: 1em;\n\n        //  [2]  Now re-style the checkboxes and radios\n        //  --------------------------------------------------------------------\n        margin: 0;\n        font-size: inherit;\n        background-color: var(--check-radio--bgc);\n        border: var(--su1) solid var(--check-radio--bc);\n        outline: 0;\n        box-shadow: none;\n        cursor: pointer;\n        -webkit-appearance: none;\n           -moz-appearance: none;\n                appearance: none;\n\n        &::-ms-check {\n            display: none;\n        }\n\n        &:focus-visible,\n        &:checked:focus-visible {\n          --check-radio--bc: var(--check-radio--color);\n\n          outline: 0;\n          box-shadow: var(--bs-focus-ring);\n        }\n\n        &:checked {\n            --check-radio--bc: var(--check-radio--color);\n        }\n    }\n\n    flex: 0 auto;\n    align-self: flex-start;\n\n    //  --  DISABLED\n    &[disabled],\n    &--disabled {\n        --check-radio--color: var(--black-200);\n        --check-radio--bc: var(--black-200);\n        --check-radio--bgc: var(--black-075);\n\n        cursor: not-allowed;\n    }\n}\n\n\n//  ============================================================================\n//  $   RADIO / CHECKBOX WRAPPERS\n//  ----------------------------------------------------------------------------\n.d-checkbox-group,\n.d-radio-group {\n    display: flex;\n    padding-right: var(--su1);\n    padding-bottom: var(--su2);\n    padding-left: var(--su1);\n\n    //  --  Wrapper Disabled State\n    &.d-checkbox-group--disabled,\n    &.d-radio-group--disabled {\n        .d-checkbox__label,\n        .d-radio__label {\n            color: var(--black-200);\n            cursor: not-allowed;\n        }\n    }\n}\n\n\n//  ============================================================================\n//  $   INPUT BLOCKS\n//      This holds the radio or checkbox input\n//  ----------------------------------------------------------------------------\n.d-checkbox__input,\n.d-radio__input {\n    display: flex;\n    align-self: flex-start;\n    padding-top: var(--su2);\n}\n\n\n//  ============================================================================\n//  $   COPY BLOCKS\n//      This holds the label and description copy\n//  ----------------------------------------------------------------------------\n.d-checkbox__copy,\n.d-radio__copy {\n    display: inline-flex;\n    flex-direction: column;\n    margin-left: var(--su8);\n}\n\n.d-checkbox__label,\n.d-radio__label {\n    display: inline-flex;\n    flex: 1 auto;\n    flex-direction: column;\n    color: var(--black-800);\n    font-weight: var(--fw-normal);\n    font-size: var(--fs14);\n    line-height: calc(20/14);\n    cursor: pointer;\n}\n\n\n//  ============================================================================\n//  $   CHECKBOXES\n//  ----------------------------------------------------------------------------\n.d-checkbox {\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        background-repeat: no-repeat;\n        background-position: center center;\n        background-size: contain;\n        border-radius: var(--br2);\n\n        &:focus-visible,\n        &:checked:focus-visible {\n            border-radius: var(--br2);\n        }\n\n        &:checked {\n            --check-radio--bgc: var(--check-radio--color);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='%23fff'/%3E%3C/svg%3E\");\n\n            //  Disabled\n            &[disabled] {\n                --check-radio--bgc: var(--black-075);\n\n                background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n            }\n        }\n\n        &--disabled:checked {\n            --check-radio--bgc: var(--black-075);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n        }\n\n        &--indeterminate,\n        &:indeterminate {\n            --check-radio--bgc: var(--check-radio--color);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='%23fff'/%3E%3C/svg%3E\");\n\n            &[disabled] {\n                --check-radio--bgc: var(--black-075);\n\n                background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n            }\n        }\n\n        &--indeterminate&--disabled,\n        &:indeterminate&--disabled {\n            --check-radio--bgc: var(--black-075);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n        }\n\n        //  Disabled\n        &[disabled],\n        &--disabled {\n            --check-radio--color: var(--black-200);\n            --check-radio--bc: var(--black-200);\n            --check-radio--bgc: var(--black-075);\n        }\n    }\n}\n\n//  $$  CHECKBOX VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-checkbox--warning {\n    --check-radio--color: var(--warning-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n        box-shadow: var(--bs-focus-ring-warning);\n    }\n}\n\n.d-checkbox--error {\n    --check-radio--color: var(--error-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n        box-shadow: var(--bs-focus-ring-error);\n    }\n}\n\n.d-checkbox--success {\n    --check-radio--color: var(--success-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n        box-shadow: var(--bs-focus-ring-success);\n    }\n}\n\n\n//  ============================================================================\n//  $   RADIOS\n//  ----------------------------------------------------------------------------\n.d-radio {\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        border-radius: var(--br-circle);\n\n        // Disabled\n        &[disabled],\n        &--disabled {\n            --check-radio--color: var(--black-200);\n            --check-radio--bc: var(--black-200);\n            --check-radio--bgc: var(--black-075);\n        }\n\n        &:focus-visible,\n        &:checked:focus-visible {\n          box-shadow: var(--bs-focus-ring), inset 0 0 0 var(--su2) var(--white);\n        }\n\n        &:checked {\n            --check-radio--bgc: var(--check-radio--color);\n\n            box-shadow: inset 0 0 0 var(--su2) var(--white);\n\n            // Disabled\n            &[disabled] {\n                --check-radio--bgc: var(--black-200);\n            }\n        }\n\n        &--disabled:checked {\n            --check-radio--bgc: var(--black-200);\n        }\n    }\n}\n\n//  $$  RADIO VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-radio--warning {\n    --check-radio--color: var(--warning-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n      box-shadow: var(--bs-focus-ring-warning), inset 0 0 0 var(--su2) var(--white);\n    }\n}\n\n.d-radio--error {\n    --check-radio--color: var(--error-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n      box-shadow: var(--bs-focus-ring-error), inset 0 0 0 var(--su2) var(--white);\n    }\n}\n\n.d-radio--success {\n    --check-radio--color: var(--success-color);\n    --check-radio--bc: var(--check-radio--color);\n\n    &:focus-visible,\n    &:checked:focus-visible {\n      box-shadow: var(--bs-focus-ring-success), inset 0 0 0 var(--su2) var(--white);\n    }\n}\n","//@import (reference) '../variables/sizes.less';\n//@import (reference) '../variables/icons.less';\n//@import (reference) 'input.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: SELECT MENUS\n//\n//  These are select menu classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/select\n//\n//  TABLE OF CONTENTS\n//   SELECT MENUS\n//    - Sizes\n//   SELECT MENU INPUTS\n//    - Validation States\n//\n\n//  ============================================================================\n//  $  SELECT MENU (NATIVE)\n//  ----------------------------------------------------------------------------\n.d-select {\n  //  Component CSS Vars\n  //  --------------------------------------------------------------------------\n  --select--bc: var(--black-400);\n  --select--bgc: var(--white);\n  --select--fc: var(--black-800);\n  --select--bs: var(--focus-ring);\n\n  position: relative;\n  display: inline-flex; // important to the children alignment\n  box-sizing: border-box;\n  width: 100%; // important to fill the available space\n  width: -webkit-fill-available; // important to fill the available space\n  font-weight: inherit;\n  font-size: var(--fs18);\n  font-family: inherit;\n  line-height: var(--lh-tight);\n\n  //  --  Arrows\n  &::before,\n  &::after {\n    position: absolute;\n    right: 0.75em;\n    z-index: var(--zi-selected);\n    border-color: var(--black-900) transparent;\n    border-style: solid;\n    border-width: 0.25em;\n    content: '';\n    pointer-events: none;\n  }\n\n  &::before {\n    top: calc(~'50% - 0.25em');\n    border-top-width: 0;\n  }\n\n  &::after {\n    top: calc(~'50% + 0.125em');\n    border-bottom-width: 0;\n  }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-select--xs {\n  font-size: var(--fs14);\n\n  .d-select__input {\n    .input-button-xs();\n  }\n}\n\n.d-select--sm {\n  font-size: var(--fs16);\n\n  .d-select__input {\n    .input-button-sm();\n  }\n}\n\n.d-select--lg {\n  font-size: var(--fs20);\n\n  .d-select__input {\n    .input-button-lg();\n  }\n}\n\n.d-select--xl {\n  font-size: var(--fs24);\n\n  .d-select__input {\n    .input-button-xl();\n  }\n}\n\n//  $  SELECT MENU INPUT\n//  ----------------------------------------------------------------------------\n.d-select__input {\n  height: 100%; // Fill the height of its parent\n  padding: 0.7rem 2em 0.7rem 0.8rem !important;\n  color: var(--select--fc);\n  background-color: var(--select--bgc);\n  border-color: var(--select--bc);\n  //  [1] Reset the appearance\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n\n  //  [2] Update the styles\n  .d-input();\n\n  --select--bc: var(--black-500);\n\n  &:focus {\n    --select--bc: var(--primary-color);\n\n    box-shadow: var(--bs-focus-ring);\n  }\n\n  &:disabled {\n    color: var(--black-400);\n    background: var(--black-075);\n    border: 1px solid var(--black-200);\n  }\n\n  &::-moz-focus-inner {\n    outline: none !important;\n  }\n\n  &:-moz-focusring {\n    color: transparent;\n    text-shadow: 0 0 0 var(--black-900);\n  }\n\n  &::-ms-expand {\n    display: none;\n  }\n}\n\n//  $$  VALIDATION STATES\n//  ------------------------------------------------------------------------\n.d-select__input--success {\n  --select--bc: var(--success-color);\n\n  &:focus {\n    --select--bc: var(--success-color);\n\n    box-shadow: var(--bs-focus-ring-success) !important;\n  }\n}\n\n.d-select__input--error {\n  --select--bc: var(--error-color);\n\n  &:focus {\n    --select--bc: var(--error-color);\n\n    box-shadow: var(--bs-focus-ring-error) !important;\n  }\n}\n\n.d-select__input--warning {\n  --select--bc: var(--warning-color);\n\n  &:focus {\n    --select--bc: var(--warning-color);\n\n    box-shadow: var(--bs-focus-ring-warning) !important;\n  }\n}\n\n.d-select--disabled {\n  &::before,\n  &::after {\n    border-color: var(--black-400) transparent;\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: SKELETON\n//\n//  These are the styles for skeleton component.\n//  The --placeholder-from-color and --placeholder-to-color custom properties can be set on the parent class of the\n//  placeholder to control the animation colors.\n//  For further documentation of these styles, please visit https://dialpad.design/components/skeleton\n\n//  ============================================================================\n//  @   SKELETON CONTAINER\n//  ----------------------------------------------------------------------------\n.skeleton-placeholder {\n  display: flex;\n  width: 100%;\n  background: var(--placeholder-from-color, @black-075);\n  animation-duration: 1000ms;\n  fill: var(--placeholder-from-color, @black-075);\n  stroke: none;\n\n  &--animate {\n    animation-name: placeholder-throb;\n    animation-iteration-count: infinite;\n  }\n}\n\n// the animation is used by the skeleton component\n@keyframes placeholder-throb {\n  10% {\n    background: var(--placeholder-from-color, @black-075);\n    fill: var(--placeholder-from-color, @black-075);\n  }\n\n  50% {\n    background: var(--placeholder-to-color, @black-050);\n    fill: var(--placeholder-to-color, @black-050);\n  }\n\n  90% {\n    background: var(--placeholder-from-color, @black-075);\n    fill: var(--placeholder-from-color, @black-075);\n  }\n}\n","//@import (reference) '../utilities/typography.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TABLES\n//\n//  These are table component classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tables\n//\n//  TABLE OF CONTENTS\n//   DEFAULT STYLE\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-table {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --table--th: var(--black-700);\n    --table--td: var(--black-600);\n    --table--bc: var(--black-200);\n\n    display: table;\n    box-sizing: border-box;\n    width: 100%;\n    max-width: 100%;\n    margin: var(--su0);\n    font-size: var(--fs14);\n    line-height: var(--lh2);\n    border-collapse: collapse;\n    border-spacing: 0;\n\n    // Caption styles\n    .d-table__caption {\n      margin-bottom: var(--su8);\n      color: var(--table--th);\n      font-weight: var(--fw-bold);\n      font-size: var(--fs18);\n      .d-ta-unset();\n    }\n\n    // Table Head Styles\n    thead {\n        border-bottom: var(--su2) solid var(--table--bc);\n\n        // Column Header Styles\n        th {\n            font-size: var(--fs12);\n            text-transform: uppercase;\n        }\n    }\n\n    // Header Styles\n    th {\n        color: var(--table--th);\n        font-weight: var(--fw-bold);\n    }\n\n    // Cell Styles\n    td {\n        color: var(--table--td);\n    }\n\n    // Header & Cell Styles\n    th,\n    td {\n        padding: var(--su16);\n        text-align: left;\n        border-bottom: var(--su1) solid var(--table--bc);\n    }\n\n    // Nested Table Body, First Row Styles, used internally on the Dialtone site\n    tbody + tbody tr:first-of-type {\n        // Header & Cell Styles\n        td,\n        th {\n            border-top: var(--su2) solid var(--table--bc);\n        }\n    }\n\n    // Last Table Row Styles\n    tbody tr:last-of-type {\n        // Header & Cell Styles\n        td,\n        th {\n            border-bottom-width: 0;\n        }\n    }\n}\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-table--inverted {\n    --table--th: var(--black-050);\n    --table--td: var(--black-100);\n    --table--bc: var(--black-050);\n}\n\n//  ============================================================================\n//  $   STRIPED ROW BACKGROUNDS\n//  ----------------------------------------------------------------------------\n.d-table--striped {\n    // Row Styles\n    tr:nth-child(even) {\n        background-color: hsl(var(--black-400-h), var(--black-400-s), var(--black-400-l), 0.1);\n    }\n\n    &.d-table--inverted {\n        // Row Styles\n        tr:nth-child(even) {\n            background-color: hsl(var(--black-050-h), var(--black-050-s), var(--black-050-l), 0.1);\n        }\n    }\n}\n","//@import (reference) '../variables/typography.less';\n//@import (reference) '../variables/spacing.less';\n//@import (reference) '../utilities/internals.less';\n//@import (reference) '../utilities/grid.less';\n\n//\n//  DIALTONE\n//  UTILTIES: TYPOGRAPHY\n//\n//  These are utility typography classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilties/typography\n//\n//  TABLE OF CONTENTS\n//   FONT-FACE\n//   FONT FAMILY\n//   RESET HEADERS & PARAGRAPH MARGINS\n//   FONT SIZE\n//   FONT STYLE\n//   FONT WEIGHT\n//   LINE HEIGHT\n//   LISTS\n//   TEXT ALIGN\n//   TEXT DECORATION\n//   TEXT OVERFLOW\n//   TEXT TRANSFORM\n//   VERTICAL ALIGN\n//   WHITESPACE\n//   WORDBREAK\n//\n//  ============================================================================\n//  $   FONT FACE\n//      To turn on custom webfonts, set @define-font-face to true.\n//  ----------------------------------------------------------------------------\n#d-internals #font-face(@define-ff-custom, @define-ff-custom-name);\n#d-internals #font-face(@define-ff-custom-italic, @define-ff-custom-name, italic);\n#d-internals #font-face(@define-ff-mono, @define-ff-mono-name);\n#d-internals #font-face(@define-ff-marketing, @define-ff-marketing-name);\n#d-internals #font-face(@define-ff-marketing-semi-expanded, @define-ff-marketing-semi-expanded-name);\n#d-internals #font-face(@define-ff-marketing-expanded, @define-ff-marketing-expanded-name);\n\n//  ============================================================================\n//  $   FONT FAMILY\n//  ----------------------------------------------------------------------------\n.d-ff-custom { font-family: @ff-custom !important; }\n.d-ff-sans { font-family: @ff-sans !important; }\n.d-ff-mono { font-family: @ff-mono !important; }\n.d-ff-marketing { font-family: @ff-marketing !important; }\n.d-ff-marketing-semi-expanded { font-family: @ff-marketing-semi-expanded !important; }\n.d-ff-marketing-expanded { font-family: @ff-marketing-expanded !important; }\n.d-ff-unset { font-family: unset !important; }\n\n//  ============================================================================\n//  $   RESET HEADERS & PARAGRAPH MARGINS\n//  ----------------------------------------------------------------------------\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nol,\nul {\n    margin-top: var(--su0);\n    margin-bottom: var(--su0);\n}\n\n//  ============================================================================\n//  $   FONT SIZE\n//  ----------------------------------------------------------------------------\n.d-fs10 { font-size: var(--fs10) !important; }\n.d-fs11 { font-size: var(--fs11) !important; }\n.d-fs12 { font-size: var(--fs12) !important; }\n.d-fs14 { font-size: var(--fs14) !important; }\n.d-fs16 { font-size: var(--fs16) !important; }\n.d-fs18 { font-size: var(--fs18) !important; }\n.d-fs20 { font-size: var(--fs20) !important; }\n.d-fs24 { font-size: var(--fs24) !important; }\n.d-fs28 { font-size: var(--fs28) !important; }\n.d-fs32 { font-size: var(--fs32) !important; }\n.d-fs36 { font-size: var(--fs36) !important; }\n.d-fs42 { font-size: var(--fs42) !important; }\n.d-fs48 { font-size: var(--fs48) !important; }\n.d-fs54 { font-size: var(--fs54) !important; }\n\n.d-headline10 { .d-fs10(); font-weight: @headline-weight; .d-tt-uppercase(); }\n.d-headline24 { .d-fs24(); font-weight: @headline-weight !important; }\n.d-headline28 { .d-fs28(); font-weight: @headline-weight !important; }\n.d-headline32 { .d-fs32(); font-weight: @headline-weight !important; }\n.d-headline36 { .d-fs36(); font-weight: @headline-weight !important; }\n.d-headline42 { .d-fs42(); font-weight: @headline-weight !important; }\n.d-headline48 { .d-fs48(); font-weight: @headline-weight !important; }\n.d-headline54 { .d-fs54(); font-weight: @headline-weight !important; }\n\n.d-fs-unset { font-size: unset !important; }\n\n\n//  ============================================================================\n//  $$  FONT STYLE\n//  ----------------------------------------------------------------------------\n.d-fs-normal { font-style: normal !important; }\n.d-fs-italic { font-style: italic !important; }\n.d-fs-unset { font-style: unset !important; }\n\n\n//  ============================================================================\n//  $$  FONT WEIGHT\n//  ----------------------------------------------------------------------------\n.d-fw-thin { font-weight: var(--fw-thin) !important; }\n.d-fw-light { font-weight: var(--fw-light) !important; }\n.d-fw-normal { font-weight: var(--fw-normal) !important; }\n.d-fw-medium { font-weight: var(--fw-medium) !important; }\n.d-fw-semibold { font-weight: var(--fw-semibold) !important; }\n.d-fw-bold { font-weight: var(--fw-bold) !important; }\n.d-fw-black { font-weight: var(--fw-black) !important; }\n.d-fw-unset { font-weight: unset !important; }\n\n\n//  ============================================================================\n//  $  LINE HEIGHT\n//  ----------------------------------------------------------------------------\n.d-lh0 { line-height: var(--lh0) !important; }\n.d-lh1 { line-height: var(--lh1) !important; }\n.d-lh2 { line-height: var(--lh2) !important; }\n.d-lh4 { line-height: var(--lh4) !important; }\n.d-lh6 { line-height: var(--lh6) !important; }\n.d-lh8 { line-height: var(--lh8) !important; }\n.d-lh12 { line-height: var(--lh12) !important; }\n.d-lh16 { line-height: var(--lh16) !important; }\n.d-lh20 { line-height: var(--lh20) !important; }\n.d-lh24 { line-height: var(--lh24) !important; }\n.d-lh-tighter { line-height: var(--lh-tighter) !important; }\n.d-lh-tight { line-height: var(--lh-tight) !important; }\n.d-lh-normal { line-height: var(--lh-normal) !important; }\n.d-lh-loose { line-height: var(--lh-loose) !important; }\n.d-lh-looser { line-height: var(--lh-looser) !important; }\n.d-lh-unset { line-height: unset !important; }\n\n\n//  ============================================================================\n//  $   LISTS\n//  ----------------------------------------------------------------------------\n.d-ls-reset {\n    //  We want to reset everything when applied\n    &,\n    li,\n    ol,\n    ul {\n        margin: @su0;\n        padding: @su0;\n        .d-ls-none();\n    }\n}\n.d-ls-none { list-style: none !important; }\n.d-lst-disc { list-style-type: disc !important; }\n.d-lst-circle { list-style-type: circle !important; }\n.d-lst-content { list-style-type: var(--ls-content) !important; }\n.d-lst-decimal { list-style-type: decimal !important; }\n.d-lst-none { list-style-type: none !important; }\n\n\n//  ============================================================================\n//  $$  TEXT ALIGN\n//  ----------------------------------------------------------------------------\n.d-ta-left { text-align: left !important; }\n.d-ta-right { text-align: right !important; }\n.d-ta-center { text-align: center !important; }\n.d-ta-justify { text-align: justify !important; }\n.d-ta-unset { text-align: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT DECORATION\n//  ----------------------------------------------------------------------------\n.d-td-none { text-decoration: none !important; }\n.d-td-underline { text-decoration: underline !important; }\n.d-td-dotted { text-decoration: underline dotted !important; }\n.d-td-line-through { text-decoration: line-through !important; }\n.d-td-unset { text-decoration: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT OVERFLOW\n//  ----------------------------------------------------------------------------\n.d-to-ellipsis { text-overflow: ellipsis !important; }\n.d-to-clip { text-overflow: clip !important; }\n.d-to-unset { text-overflow: unset !important; }\n\n//  $$  OVERFLOW WRAP\n//  ----------------------------------------------------------------------------\n.d-ow-normal { overflow-wrap: normal !important; }\n.d-ow-anywhere { overflow-wrap: anywhere !important; }\n.d-ow-break-word { overflow-wrap: break-word !important; }\n.d-ow-unset { overflow-wrap: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT TRANSFORM\n//  ----------------------------------------------------------------------------\n.d-tt-uppercase { text-transform: uppercase !important; }\n.d-tt-lowercase { text-transform: lowercase !important; }\n.d-tt-capitalize { text-transform: capitalize !important; }\n.d-tt-full-width { text-transform: full-width !important; }\n.d-tt-none { text-transform: none !important; }\n.d-tt-unset { text-transform: unset !important; }\n\n\n//  ============================================================================\n//  $$  VERTICAL ALIGN\n//  ----------------------------------------------------------------------------\n.d-va-baseline { vertical-align: baseline !important; }\n.d-va-top { vertical-align: top !important; }\n.d-va-bottom { vertical-align: bottom !important; }\n.d-va-text-top { vertical-align: text-top !important; }\n.d-va-text-bottom { vertical-align: text-bottom !important; }\n.d-va-middle { vertical-align: middle !important; }\n.d-va-sub { vertical-align: sub !important; }\n.d-va-super { vertical-align: super !important; }\n.d-va-unset { vertical-align: unset !important; }\n\n\n//  ============================================================================\n//  $$  WHITESPACE\n//  ----------------------------------------------------------------------------\n.d-ws-normal { white-space: normal !important; }\n.d-ws-nowrap { white-space: nowrap !important; }\n.d-ws-pre { white-space: pre !important; }\n.d-ws-pre-line { white-space: pre-line !important; }\n.d-ws-pre-wrap { white-space: pre-wrap !important; }\n.d-ws-break-spaces { white-space: break-spaces !important; }\n.d-ws-unset { white-space: unset !important; }\n\n\n//  ============================================================================\n//  $$  WORD BREAK\n//  ----------------------------------------------------------------------------\n.d-wb-normal { word-break: normal !important; }\n.d-wb-break-all { word-break: break-all !important; }\n.d-wb-keep-all { word-break: keep-all !important; }\n.d-wb-unset { word-break: unset !important; }\n\n\n\n// Unsure what to do with this\n//  $$  TRUNCATE\n//  ----------------------------------------------------------------------------\n.d-truncate {\n    overflow: hidden !important;\n    white-space: nowrap !important;\n    text-overflow: ellipsis !important;\n}\n","//\n//  DIALTONE\n//  COMPONENTS: Tabs\n//\n//  These are tab classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tabs\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//   ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   WRAPPERS\n//  ----------------------------------------------------------------------------\n.d-tablist {\n    position: relative;\n    display: flex;\n    flex-wrap: wrap;\n    color: var(--fc-light);\n    font-size: var(--fs16);\n\n    // Add a bottom border unless no border is requested\n    &:not(.d-tablist--no-border)::after {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        z-index: var(--zi-base1);\n        height: var(--su1);\n        background-color: var(--black-200);\n        content: '';\n    }\n}\n\n\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-tab {\n    //  --  COMPONENT CSS VARS\n    //  ------------------------------------------------------------------------\n    --tab--bgc: hsla(var(--white-hsl) ~' / ' 0%);\n    --tab--fc: inherit;\n    --tab--oc: var(--focus-ring);\n\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    margin: 0 var(--su2);\n    padding: var(--su12);\n    color: var(--tab--fc);\n    font: inherit;\n    line-height: 1;\n    background-color: var(--tab--bgc);\n    border: 0;\n    border-radius: var(--br4) var(--br4) 0 0;\n    cursor: pointer;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: var(--td50);\n    transition-property: background-color, color;\n    fill: currentColor;\n\n\n    &:first-of-type {\n        margin-left: 0;\n    }\n\n    &:last-of-type {\n        margin-right: 0;\n    }\n\n    &::after {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        height: var(--su2);\n        background-color: var(--tab--bgc);\n        content: '';\n    }\n\n    &:focus-visible {\n        outline: none;\n        box-shadow: 0 0 0 var(--su2) var(--tab--oc);\n    }\n\n    &:hover {\n        --tab--bgc: var(--black-025);\n        --tab--fc: var(--fc-dark);\n    }\n\n    //  Turn off animations if someone doesn't want them.\n    @media (prefers-reduced-motion) {\n        transition: none;\n    }\n}\n\n\n//  ============================================================================\n//  $   SELECTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tab--selected {\n    --tab--fc: var(--primary-color);\n\n    z-index: var(--zi-selected);\n\n    &:hover {\n        --tab--bgc: hsla(var(--primary-color-hsl) ~' / ' 10%);\n        --tab--fc: var(--primary-color-hover);\n    }\n\n    &::after,\n    &:hover::after {\n        --tab--bgc: var(--primary-color);\n    }\n}\n\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tablist--inverted {\n    --tab--fc: var(--white);\n\n    &::after {\n        background-color: hsla(var(--purple-800-hsl) ~' / ' 50%);\n    }\n\n    .d-tab {\n        --tab--fc: var(--white);\n\n        &:hover {\n            --tab--bgc: hsla(var(--white-hsl) ~' / ' 10%);\n        }\n    }\n\n    .d-tab--selected {\n        --tab--fc: var(--white);\n        --tab--oc: var(--white);\n\n        &::after {\n            --tab--bgc: currentColor;\n        }\n    }\n}\n\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n.d-tablist--sm {\n    font-size: var(--fs12);\n\n    .d-tab {\n        padding: var(--su12);\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: TOOLTIPS\n//\n//  These are tooltip classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tooltips\n//\n//  TABLE OF CONTENTS\n//   VARIABLES\n//   BASE STYLE\n//   ALTERNATE STYLES\n//   ARROW DIRECTIONS\n//    - TOP LEFT\n//    - TOP CENTER\n//    - TOP RIGHT\n//    - RIGHT TOP\n//    - RIGHT CENTER\n//    - RIGHT BOTTOM\n//    - BOTTOM LEFT\n//    - BOTTOM CENTER\n//    - BOTTOM RIGHT\n//    - LEFT TOP\n//    - LEFT CENTER\n//    - LEFT BOTTOM\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip-container {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.d-tooltip {\n    //  --  COMPONENT CSS VARS\n    //  -----------------------\n    --tooltip--bgc: var(--black-800);\n    --tooltip--fc: var(--white);\n\n    z-index: var(--zi-tooltip);\n    max-width: var(--size216);\n    padding: var(--su8);\n    color: var(--tooltip--fc);\n    font-size: var(--fs12);\n    line-height: var(--lh-normal);\n    text-align: center;\n    background-color: var(--tooltip--bgc);\n    border-radius: var(--br4);\n\n    &::after {\n        position: absolute;\n        border: var(--su6) solid transparent;\n        content: '';\n    }\n}\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--inverted {\n    //  --  COMPONENT CSS VARS\n    //  -----------------------\n    --tooltip--bgc: var(--black-050);\n    --tooltip--fc: var(--black-700);\n}\n\n//  ============================================================================\n//  $   HIDE STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--hide {\n    visibility: hidden;\n    opacity: 0;\n    transition:\n        visibility 0s 200ms,\n        transform 200ms var(--ttf-in-out) 0s,\n        opacity 200ms var(--ttf-in-out) 0s;\n}\n\n//  ============================================================================\n//  $   SHOW STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--show {\n    transform: translate(0, 0);\n    visibility: visible;\n    opacity: 1;\n    transition:\n        visibility 0s 0s,\n        transform 200ms var(--ttf-in-out) 10ms,\n        opacity 200ms var(--ttf-in-out) 10ms;\n}\n\n//  ============================================================================\n//  $   HOVER AND FOCUS STYLES\n//  ----------------------------------------------------------------------------\n.d-tooltip--hover {\n    .d-tooltip {\n        &:extend(.d-tooltip--hide);\n    }\n\n    &:hover,\n    &:focus-visible {\n        .d-tooltip {\n            &:extend(.d-tooltip--show);\n        }\n    }\n}\n\n//  ============================================================================\n//  $   TOOLTIP DIRECTIONS\n//  ============================================================================\n//  $$  TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--top-right {\n  top: calc(100% + var(--su12));\n  transform: translateY(var(--su16));\n\n  &::after {\n    top: var(--sun6);\n    border-top-width: 0;\n    border-bottom-color: var(--tooltip--bgc);\n  }\n}\n\n//  $$  BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--bottom-left,\n.d-tooltip__arrow--bottom-center,\n.d-tooltip__arrow--bottom-right {\n  bottom: calc(100% + var(--su12));\n  transform: translateY(var(--sun16));\n\n  &::after {\n    bottom: var(--sun6);\n    border-top-color: var(--tooltip--bgc);\n    border-bottom-width: 0;\n  }\n}\n\n//  $$  TOP / BOTTOM LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--bottom-left {\n  left: var(--sun2);\n\n  &::after {\n    left: var(--su16);\n  }\n}\n\n//  $$  TOP / BOTTOM CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--bottom-center {\n  &::after {\n    left: 50%;\n    margin-left: var(--sun6);\n  }\n}\n\n//  $$  TOP / BOTTOM RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--top-right,\n.d-tooltip__arrow--bottom-right {\n  right: var(--sun2);\n\n  &::after {\n    right: var(--su16);\n  }\n}\n\n//  $$  RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--right-bottom {\n  right: calc(100% + var(--su12));\n  transform: translateX(var(--sun16));\n\n  &::after {\n    right: var(--sun6);\n    border-right-width: 0;\n    border-left-color: var(--tooltip--bgc);\n  }\n}\n\n//  $$  LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow--left-top,\n.d-tooltip__arrow--left-center,\n.d-tooltip__arrow--left-bottom {\n  left: calc(100% + var(--su12));\n  transform: translateX(var(--su16));\n\n  &::after {\n    left: var(--sun6);\n    border-right-color: var(--tooltip--bgc);\n    border-left-width: 0;\n  }\n}\n\n//  $$  RIGHT / LEFT TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--left-top {\n  top: var(--sun1);\n\n  &::after {\n    top: var(--su8);\n  }\n}\n\n//  $$  RIGHT / LEFT CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--left-center {\n  &::after {\n    top: 50%;\n    margin-top: var(--sun6);\n  }\n}\n\n//  $$  RIGHT / LEFT BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-bottom,\n.d-tooltip__arrow--left-bottom {\n  bottom: var(--sun1);\n\n  &::after {\n    bottom: var(--su8);\n  }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOGGLE\n//\n//  These are toggle classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/toggle\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//   CHECKED STYLES\n//   DISABLED STYLES\n//\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toggle {\n  //  Component specific CSS Vars\n  //  ------------------------------------------------------------------------\n  --toggle--ta: var(--ttf-in-out);\n  --toggle-ts: var(--td200);\n  --toggle-bc: var(--black-200);\n\n  position: relative;\n  display: inline-block;\n  box-sizing: border-box;\n  width: var(--su48);\n  height: var(--su24);\n  padding: 0;\n  line-height: var(--lh4);\n  vertical-align: middle;\n  background-color: var(--toggle-bc);\n  border: var(--su1) solid var(--toggle-bc);\n  border-radius: var(--su48);\n  cursor: pointer;\n\n  &__inner {\n    position: absolute;\n    top: calc(var(--su4) - var(--su1));\n    left: var(--su24);\n    width: var(--su16);\n    height: var(--su16);\n    color: var(--white);\n    background-image: url(\"data:image/svg+xml;charset=UTF-8, %3csvg viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg' %3e%3cpath d='M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z' fill='%23fff' /%3e%3c/svg%3e\");\n    background-repeat: no-repeat;\n  }\n\n  &::after {\n    position: absolute;\n    top: var(--su1);\n    left: var(--su1);\n    width: calc(var(--su16) + var(--su4));\n    height: calc(var(--su16) + var(--su4));\n    background-color: var(--white);\n    border-radius: var(--br-circle);\n    cursor: pointer;\n    transition: left var(--toggle-ts) var(--toggle--ta);\n    content: ' ';\n  }\n\n  &:focus-visible {\n    outline: none;\n    box-shadow: var(--bs-focus-ring);\n  }\n}\n\n//  $$  CHECKED TOGGLE\n//  ----------------------------------------------------------------------------\n.d-toggle--checked {\n  --toggle-bc: var(--primary-color);\n\n  .d-toggle__inner {\n    left: var(--su6);\n    background-image: url(\"data:image/svg+xml,%3Csvg viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='%23fff'/%3E%3C/svg%3E\");\n  }\n\n  &::after {\n    left: calc(var(--su24) + var(--su1));\n  }\n}\n\n//  $$  DISABLED TOGGLE\n//  ----------------------------------------------------------------------------\n.d-toggle--disabled,\n .d-toggle[disabled] {\n  cursor: not-allowed;\n  opacity: 0.5;\n\n  &::after {\n    cursor: not-allowed;\n    transition-property: none;\n  }\n}\n","//\n//  DIALTONE\n//  TOKENS: ICONS\n//\n//  These are icon variables for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/\n//\n//  ============================================================================\n//  $   ICON SIZES\n//  ----------------------------------------------------------------------------\n//  OLD VARIABLES\n@icon-size14:     1.4rem;\n@icon-size16:     1.6rem;\n@icon-size18:     1.8rem;\n@icon-size20:     2rem;\n@icon-size24:     2.4rem;\n@icon-size32:     3.2rem;\n@icon-size48:     4.8rem;\n@icon-size64:     6.4rem;\n\n//  NEW VARIABLES\n@icon12:                        1.2rem;\n@icon14:                        1.4rem;\n@icon16:                        1.6rem;\n@icon18:                        1.8rem;\n@icon20:                        2rem;\n@icon24:                        2.4rem;\n@icon32:                        3.2rem;\n@icon48:                        4.8rem;\n@icon64:                        6.4rem;\n\n@icon-vars: {\n  icon12:                       @icon12;\n  icon14:                       @icon14;\n  icon16:                       @icon16;\n  icon18:                       @icon18;\n  icon20:                       @icon20;\n  icon24:                       @icon24;\n  icon32:                       @icon32;\n  icon48:                       @icon48;\n  icon64:                       @icon64;\n}\n","//\n//  DIALTONE\n//  INTERNAL MIXINS\n//\n//  These are internal mixins to help generate various classes.\n//\n//  TABLE OF CONTENTS\n//   AUTO GENERATING CLASSES\n//\n//  ============================================================================\n//  $   CONFIGURATION SETTINGS\n//      Various configuration settings to generate the classes desired.\n//  ----------------------------------------------------------------------------\n#d-internal-config {\n\n    //  Do we want to generate font-face CSS?\n    @generate-font-face:             true;\n\n    //  What's the font-face file path?\n    @define-font-path:               '../fonts/';\n\n}\n\n//  ============================================================================\n//  $   INTERNAL MIXINS\n//      Various mixins used to build Dialtone\n//  ----------------------------------------------------------------------------\n#d-internals() {\n    //  ========================================================================\n    //  $   COLOR CLASS GENERATOR\n    //      Create a mixin to generate all the utility classes required. By doing it\n    //      this way, we are disabling the ability to use Dialtone utility classes as\n    //      a mixin. Previously we had allowed for utility classes to be used as\n    //      mixins, but because our overall CSS bundle size is growing exponentially,\n    //      Dialtone v6 removed this ability. It is now recommended to use CSS\n    //      variables (var(--color)). Less variables (@color) are also available,\n    //      but their use is discouraged.\n    //\n    //      USAGE:\n    //      #d-internals #color-classes(purple-500);\n    //\n    //  ------------------------------------------------------------------------\n    #color-classes(@color-name) {\n      //  --  FONT STYLES\n      .d-fc-@{color-name},\n      .h\\:d-fc-@{color-name}:hover,\n      .f\\:d-fc-@{color-name}:focus {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      }\n      .f\\:d-fc-@{color-name}:focus-within {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      }\n      .fv\\:d-fc-@{color-name}:focus-visible {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-fc-@{color-name}', {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      });\n\n\n      //  --  BORDER STYLES\n      .d-bc-@{color-name},\n      .h\\:d-bc-@{color-name}:hover,\n      .f\\:d-bc-@{color-name}:focus {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      }\n      .f\\:d-bc-@{color-name}:focus-within {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      }\n      .fv\\:d-bc-@{color-name}:focus-visible {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bc-@{color-name}', {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      });\n\n      //  --  DIVIDER COLORS\n      .d-divide-@{color-name} > * + * {\n        --dco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--dco)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-divide-@{color-name} > * + *', {\n        --dco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--dco)) !important;\n      });\n\n      //  --  BACKGROUND STYLES\n      .d-bgc-@{color-name},\n      .h\\:d-bgc-@{color-name}:hover,\n      .f\\:d-bgc-@{color-name}:focus {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      }\n      .f\\:d-bgc-@{color-name}:focus-within {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      }\n      .fv\\:d-bgc-@{color-name}:focus-visible {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgc-@{color-name}', {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      });\n    }\n\n    //  ========================================================================\n    //  $   STANDARD WEIGHT LOOP GENERATOR\n    //      Since most of our colors have similar stops (100 - 600), we'll save\n    //      ourselves the need to call the mixin for each color and create a\n    //      loop that will create most of the colors for us.\n    //\n    //      USAGE:\n    //      #d-internals #standard-stops(purple);\n    //\n    //  ------------------------------------------------------------------------\n    #standard-stops(@color) {\n      #d-internals #color-classes(~'@{color}-600');\n      #d-internals #color-classes(~'@{color}-500');\n      #d-internals #color-classes(~'@{color}-400');\n      #d-internals #color-classes(~'@{color}-300');\n      #d-internals #color-classes(~'@{color}-200');\n      #d-internals #color-classes(~'@{color}-100');\n    }\n\n    //  ========================================================================\n    //  $   HOVER / FOCUS GENERATOR\n    //      Pass a class name and mixin with the properties to copy into this function\n    //      to generate hover / focus copies of it prefixed with h: and f:.\n    //      Many of the class generators in this file will already do this,\n    //      so this mixin is meant more for one off exceptions.\n    //\n    //      USAGE:\n    //      #d-internals #generate-hover-focus(d-bgc-transparent, {.d-bgc-transparent();});\n    //\n    //  ------------------------------------------------------------------------\n    #generate-hover-focus(@className, @mixin) {\n      .h\\:@{className}:hover {\n        @mixin();\n      }\n      .f\\:@{className}:focus {\n        @mixin();\n      }\n      .f\\:@{className}:focus-within {\n        @mixin();\n      }\n      .fv\\:@{className}:focus-visible {\n        @mixin();\n      }\n    }\n\n    //  ========================================================================\n    //  $   OPACITY CLASS GENERATOR\n    //      Creates opacity classes for font, background, and border colors.\n    //\n    //      USAGE:\n    //      #d-internals #opacity-classes(50);\n    //\n    //  ------------------------------------------------------------------------\n    #opacity-classes(@opacity) {\n      //  --  FONT OPACITY\n      .d-fco@{opacity},\n      .h\\:d-fco@{opacity}:hover,\n      .f\\:d-fco@{opacity}:focus {\n        --fco: ~'@{opacity}%' !important;\n      }\n      .f\\:d-fco@{opacity}:focus-within {\n        --fco: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-fco@{opacity}:focus-visible {\n        --fco: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-fco@{opacity}', {\n        --fco: ~'@{opacity}%' !important;\n      });\n\n\n      //  --  BORDER OPACITY\n      .d-bco@{opacity},\n      .h\\:d-bco@{opacity}:hover,\n      .f\\:d-bco@{opacity}:focus {\n        --bco: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bco@{opacity}:focus-within {\n        --bco: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bco@{opacity}:focus-visible {\n        --bco: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bco@{opacity}', {\n        --bco: ~'@{opacity}%' !important;\n      });\n\n      //  --  DIVIDER OPACITY\n      .d-dco@{opacity} > * + * {\n        --dco: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-dco@{opacity} > * + *', {\n        --dco: ~'@{opacity}%' !important;\n      });\n\n      //  --  BACKGROUND COLOR OPACITY\n      .d-bgo@{opacity},\n      .h\\:d-bgo@{opacity}:hover,\n      .f\\:d-bgo@{opacity}:focus {\n        --bgo: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bgo@{opacity}:focus-within {\n        --bgo: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bgo@{opacity}:focus-visible {\n        --bgo: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgo@{opacity}', {\n        --bgo: ~'@{opacity}%' !important;\n      });\n\n      //  --  BACKGROUND GRADIENT STARTING STOP\n      .d-bgg-from-o@{opacity},\n      .h\\:d-bgg-from-@{opacity}:hover,\n      .f\\:d-bgg-from-@{opacity}:focus {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bgg-from-@{opacity}:focus-within {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bgg-from-@{opacity}:focus-visible {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-from-@{opacity}', {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      });\n\n      //  --  BACKGROUND GRADIENT ENDING STOP\n      .d-bgg-to-o@{opacity},\n      .h\\:d-bgg-to-@{opacity}:hover,\n      .f\\:d-bgg-to-@{opacity}:focus {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bgg-to-@{opacity}:focus-within {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bgg-to-@{opacity}:focus-visible {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-to-@{opacity}', {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      });\n    }\n    //  ========================================================================\n    //  $   COLOR VARS\n    //      #color-vars creates all our HSL CSS color vars for us from a set\n    //      of HEX codes.\n    //\n    //      USAGE:\n    //      #d-internals #color-vars(@black-900);\n    //\n    //  ------------------------------------------------------------------------\n    #color-vars(@name, @color) {\n        --@{name}-h: hue(@color);\n        --@{name}-s: saturation(@color);\n        --@{name}-l: lightness(@color);\n        --@{name}-hsl: ~\"var(--@{name}-h) var(--@{name}-s) var(--@{name}-l)\";\n        --@{name}: hsla(~\"var(--@{name}-h) var(--@{name}-s) var(--@{name}-l) /  var(--alpha, 100%)\");\n    }\n    #dark-color-vars(@name, @color) {\n\n    }\n\n    //  ========================================================================\n    //  $   DARK MODE\n    //      #dark-mode creates a dark-mode override behind the\n    //      `prefers-color-scheme: dark` media query and `.theme-system` class.\n    //      It also renders the same rules outside of the media query with\n    //      `.theme-dark`.\n    //\n    //      USAGE:\n    //      #d-internals #dark-mode('.d-modal', { background-color: var(--black-200); })\n    //\n    //  ------------------------------------------------------------------------\n    #dark-mode(@class, @rules) {\n        @nodot-s: replace(@class, \"^\\.\", \"\");\n        @nodot: ~\"@{nodot-s}\";\n        @classname: ~\"@{class}\";\n\n        body.theme-system @{classname} {\n            @media (prefers-color-scheme: dark) {\n                @rules();\n            }\n        }\n        body.theme-dark @{classname} {\n            @rules();\n        }\n\n        #d-internals-collect-dark() {\n            .d\\:@{nodot} {\n                @rules();\n            }\n        }\n    }\n\n\n    //  ========================================================================\n    //  $   AUTO GENERATE CLASSES\n    //      This mixin tool is used to help generate a bunch of related classes,\n    //      usually utility classes.\n    //  ------------------------------------------------------------------------\n    #auto-generate(@class-prefix, @rules, @values) {\n        #d-internals #auto-generate-with-index(@class-prefix, @rules, 1, @values);\n    }\n    #auto-generate-with-index(@class-prefix, @rules, @index, @values) when (@index <= length(@values)) {\n        // Extract our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @px: (@val * 10); // Convert rem values to px equilavent\n            @unitless: unit(@px);\n\n            //  ...and less than 0\n            & when (@unitless < 0) {\n                @abs: abs(@unitless);\n                @class-name: ~\"@{class-prefix}n@{abs}\";\n\n                @{class-name} { @rules(); .template(@val); }\n            }\n\n            //  ...and equal to or greater than 0\n            & when (@unitless >= 0) {\n                @class-name: ~\"@{class-prefix}@{unitless}\";\n\n                @{class-name} { @rules(); .template(@val); }\n            }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index(@class-prefix, @rules, @index + 1, @values);\n    }\n\n    #auto-generate-no-convert(@class-prefix, @rules, @values) {\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, 1, @values);\n    }\n    #auto-generate-with-index-no-convert(@class-prefix, @rules, @index, @values) when (@index <= length(@values)) {\n        // Extract our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, @index + 1, @values);\n    }\n\n\n    //  ========================================================================\n    //  $   CREATE FONT FACE\n    //  ------------------------------------------------------------------------\n    #font-face(@type, @name, @style: normal) {\n        #d-internal-config();\n\n        if((@generate-font-face = true), each(@type, {\n            @font-face {\n                font-style: @style;\n                font-weight: @key;\n                font-family: @name;\n                src: url(\"@{define-font-path}@{value}.woff2\") format(\"woff2\");\n            };\n        }));\n    }\n\n    #gradient-classes(@color) {\n      //  COLOR STOP 1, STARTING STOP\n      .d-bgg-from-@{color},\n      .h\\:d-bgg-from-@{color}:hover,\n      .f\\:d-bgg-from-@{color}:focus {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .f\\:d-bgg-from-@{color}:focus-within {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .fv\\:d-bgg-from-@{color}:focus-visible {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-from-@{color}', {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      });\n\n      //  COLOR STOP 2, ENDING STOP\n      .d-bgg-to-@{color},\n      .h\\:d-bgg-to-@{color